import ImageDataProvider from './ImageDataProvider'
import VoiceDataProvider from './VoiceDataProvider'
import VideoDataProvider from './VideoDataProvider'
import StructuredMessageDataProvider from './StructuredMessageDataProvider'
import TextMessageDataProvider from './TextMessageDataProvider'
import MergeFieldDataProvider from './MergeFieldDataProvider'
import MiniProgramLinkDataProvider from './MiniProgramLinkDataProvider'

class DataProvider {
  static create (type, options) {
    let provider = null
    switch (type) {
      case 'Text Message':
        provider = new TextMessageDataProvider(options)
        break

      case 'Image':
        provider = new ImageDataProvider(options)
        break

      case 'Voice Message':
        provider = new VoiceDataProvider(options)
        break

      case 'Video':
        provider = new VideoDataProvider(options)
        break

      case 'Structured Message':
        provider = new StructuredMessageDataProvider(options)
        break

      case 'Mini Program Link':
        provider = new MiniProgramLinkDataProvider(options)
        break

      case 'Merge Field':
        provider = options.enableMergeField ? new MergeFieldDataProvider(options) : {}
        break

      default:
        console.warn(`Unknown message type. (${type})`)
        break
    }

    return provider
  }
}

export default DataProvider
