<template>
  <div class="wechat-response">
    <label class="slds-form-element__label">
      <abbr class="slds-required" v-if="required">*</abbr>Auto Reply {{ responseSelectedInfo }}
    </label>
    <div
      class="disabled-cover"
      v-show="disabled"
    ></div>
    <wechat-response-designer
      v-model="wechatResponse"
      :choosers="chooserConfigs"
      :multiple="multiple"
      :reply-mode-config="replyModeConfig"
      :limit-of-items="limitOfItems"
    >
    </wechat-response-designer>
    <div
      class="no-response"
      v-show="value.items.length === 0"
    >
      No Responses
    </div>
  </div>
</template>

<script>
import { WechatResponseDesigner } from 'charket-ui/dist/wechat-response-designer.js'
import WechatResponseHelper from './WechatResponseHelper'
import WeChatResponseSettings from '../../model/WeChatResponseSettings.js'

/*
<!-- example -->
<wechat-response
  v-model="response"
  wechat-account="wechatAccount"
  :choosers="['Text', 'Image', 'Skill']"
  multiple
  enable-reply-mode // working with multiple
/>
*/

export default {
  components: {
    WechatResponseDesigner
  },
  model: {
    prop: 'value',
    event: 'input'
  },
  props: {
    value: {
      type: Object,
      default () {
        return {}
      }
    },
    limitOfItems: {
      type: Number,
      default: 999
    },
    /**
     * {id: xxxxx, type: Service Account / Subscription Account / Mini Program}
     */
    wechatAccount: {
      type: Object,
      required: true
    },
    /**
     * [
     *   'Image',
     *   'Video',
     *   'Voice Message',
     *   'Text',
     *   'Structured Message',
     * ]
     */
    choosers: {
      type: Array,
      required: true
    },
    multiple: {
      type: Boolean,
      default () {
        return false
      }
    },
    enableReplyMode: {
      type: [Boolean, String],
      default () {
        return false
      }
    },
    pageName: {
      type: String,
      default: ''
    },
    enableMergeField: {
      type: [Boolean, String],
      default () {
        return true
      }
    },
    required: {
      type: Boolean,
      default () {
        return false
      }
    },
    disabled: {
      type: Boolean,
      default () {
        return false
      }
    }
  },
  data () {
    return {
      helperInstance: null,
      wechatResponse: {}
    }
  },
  created () {
    this.helperInstance = new WechatResponseHelper(this)
  },
  computed: {
    chooserConfigs () {
      return this.helper.getChooserConfigs(this.choosers)
    },
    replyModeConfig () {
      return this.helper.getReplyModeConfig(this.enableReplyMode)
    },
    helper () {
      return this.helperInstance
    },
    accountId () {
      return this.wechatAccount.id
    },
    responseSelectedInfo () {
      if (this.value) {
        return `(${this.value.items.length}/3)`
      }
      return '0/3'
    }
  },
  watch: {
    helper () {
      if (!this.helperInstance) {
        this.helperInstance = new WechatResponseHelper(this)
      }
    },
    wechatResponse (value) {
      if (value.isInternalChange) {
        return
      }

      const response = this.multiple
        ? WeChatResponseSettings.from(value)
        : WeChatResponseSettings.createItem(value)

      this.$emit('input', response)
    },
    value: {
      deep: true,
      immediate: true,
      handler (value) {
        this.wechatResponse = Object.assign({ isInternalChange: true }, this.value)
      }
    }
  }
}
</script>
<style lang="scss" scoped>
.plus-icon {
  width: 12px;
  height: 12px;
  fill: #0070d2;
}
.wechat-response {
  position: relative;

  .disabled-cover {
    position: absolute;
    top: 22px;
    left: 0;
    right: 0;
    height: 40px;
    background-color: rgba(255, 255, 255, 0.3);
    z-index: 1000;
    cursor: not-allowed;
  }

  .no-response {
    border: 1px solid #D8DDE6;
    border-top: none;
    height: 70px;
    display: inline-block;
    width: 100%;
    text-align: center;
    line-height: 70px;
    color: #706E6B;
  }
}
</style>
