import { MessageService } from '../../../service'

const DEFAULT_PAGE_SIZE = 20

class VideoDataProvider {
  constructor (options) {
    this.options = options
  }

  async get ({ keyword = '', limit = DEFAULT_PAGE_SIZE, offset = 0 }) {
    const messages = await MessageService.getMessages({
      type: 'Video',
      keyword,
      limit,
      offset,
      wechatAccountId: this.options.wechatAccount.id
    })

    return messages.map(message => this.options.formatter(message))
  }
}

export default VideoDataProvider
