import { SObjectTransport } from '../transport'

const sobjectTransport = new SObjectTransport()

class SObjectService {
  static async getAllMergeableSObjects (searchKey) {
    const mergeSObjects = await sobjectTransport.getAllMergeableSObjects(searchKey)
    let SObjects = []
    if (!mergeSObjects || mergeSObjects.length <= 0) {
      return SObjects
    }
    for (const SObject of mergeSObjects) {
      SObjects.push({ name: SObject.Label, value: SObject.Name, objectKey: true })
    }

    return SObjects
  }

  static async getObjectsByPageName (pageName) {
    const objects = await sobjectTransport.getObjectsByPageName(pageName)
    let objectsByPageName = []
    if (!objects || objects.length <= 0) {
      return objectsByPageName
    }
    for (const object of objects) {
      objectsByPageName.push({ name: object.Label, value: object.Name, objectKey: true })
    }

    return objectsByPageName
  }

  static async getFields (objectName) {
    const data = await sobjectTransport.getFields(objectName)

    return data.map(item => {
      return {
        name: item.Label,
        label: item.Name,
        value: item.ReferenceName,
        isReferenceType: item.IsReferenceType,
        relationshipName: item.RelationshipName,
        type: item.Type
      }
    })
  }
}

export default SObjectService
