<template>
  <button
    class="slds-button slds-button_icon btn-gap"
    v-clipboard:copy="message"
    v-clipboard:success="onCopy"
    v-clipboard:error="onError"
    :title="targetName">
    <slot>
      <icon name="copy" icon-class-name="slds-button__icon">
      </icon>
    </slot>
  </button>
</template>

<script>
export default {
  name: 'CopyButton',
  props: {
    message: {
      type: String,
      default: ''
    },
    targetName: {
      type: String,
      default: ''
    }
  },
  methods: {
    onCopy (e) {
      const content = `The ${this.targetName} has been copied to your clipboard.`
      this.$bus.emit('success', { content, type: 'success' })
    },
    onError () {
      this.$bus.emit('error', { content: 'Copy failed.', type: 'error' })
    }
  }
}
</script>

<style lang="scss" scoped>
  .btn-gap {
    margin-bottom: 4px;
  }
</style>
