import sfdcRemoting from './sfdc-remoting.js'

export function saveTextMessage (wechatAccountId, textBody) {
  return sfdcRemoting({
    method: 'MessageRemoteActions.saveTextMessage',
    args: [ wechatAccountId, textBody ]
  })
}

export function getStructuredMessage (messageId) {
  return sfdcRemoting({
    method: 'MessageRemoteActions.getStructuredMessage',
    args: [ messageId ]
  })
}

export function getSkills (numberOfRowsToSkip, numberOfRowsToReturn, searchKey = '') {
  return sfdcRemoting({
    method: 'SkillRemoteActions.getSkills',
    args: [ numberOfRowsToSkip, numberOfRowsToReturn, searchKey ]
  })
}

export function getObjects (pageName) {
  // accept arguments: 'QR Codes', 'Mass Message', 'Menu', 'SMART REPLIES', 'WELCOME MESSAGE'
  return sfdcRemoting({
    method: 'SObjectRemoteActions.getObjects',
    args: [ pageName ]
  })
}

export function getFields (objectName) {
  return sfdcRemoting({
    method: 'SObjectRemoteActions.getFields',
    args: [ objectName ]
  })
}

export function getDataBySql (sql) {
  return sfdcRemoting({
    method: 'DatabaseRemoteActions.queryData',
    args: [ sql ]
  })
}

export function updateTextMessage (textBody, messageId) {
  return sfdcRemoting({
    method: 'MessageRemoteActions.updateTextMessage',
    args: [ textBody, messageId ]
  })
}
