<template>
  <div>
    <section role="dialog" tabindex="-1" aria-labelledby="modal-heading-01" aria-modal="true" aria-describedby="response-modal-content" class="slds-modal slds-fade-in-open">
      <div class="slds-modal__container" :style="{transform: 'none', minWidth: '840px'}">
        <header class="slds-modal__header">
          <button class="slds-button slds-button_icon slds-modal__close slds-button_icon-inverse" title="Close" @click="closeNewResponse()">
            <icon name="close" icon-class-name="slds-button__icon slds-button__icon_large">
            </icon>
            <span class="slds-assistive-text">Close</span>
          </button>
          <h2 class="slds-text-heading_medium slds-hyphenate">{{ responseTitle }}</h2>
        </header>
        <div class="slds-modal__content slds-p-around_medium" id="response-modal-content" v-if="!isResponseLoading">
          <div class="toast-container" v-if="toastMsgInfo">
            <div class="slds-notify slds-theme_error notify-toast-error" role="alert">
              <span class="slds-assistive-text">info</span>
              <div class="slds-notify__content">
                <h2 class="slds-text-heading_small">{{ toastMsgInfo }}</h2>
              </div>
            </div>
          </div>

          <div class="slds-form-element form-section left-section">
            <div class="slds-form-element__control">
              <lookup
                :is-danger="resError.WeChatAccountError"
                :current="currentAccountItem"
                @selected="onSelect($event)"
                @remove="onRemove()">
              </lookup>
            </div>
          </div>
          <div class="slds-form-element form-section right-section">
            <div class="slds-form-element__control">
              <label class="slds-checkbox" for="active-checkbox">
                <input type="checkbox" name="default" id="active-checkbox" v-model="currentResponse.IsActive" />
                <span class="slds-checkbox_faux"></span>
                <span class="slds-form-element__label cursor-label">Active</span>
              </label>
            </div>
          </div>
          <div class="error-info form-section" v-show="errorToastMsg">{{ errorToastMsg }}</div>
          <div class="slds-form-element form-section multiple-section">
            <toggle
              label-info="Repeat this response within one conversation"
              :statuses="toggleStatuses"
              :is-new-opened="isNewOpened"
              @toggle="toggleMultipleReply"
            ></toggle>
          </div>
          <div class="slds-form-element form-section" :class="{'slds-has-error': resError.IntervalError || showError}">
            <div class="slds-form-element__control">
              <label class="slds-form-element__label" for="interval-time-input"><span class="red-color">*</span> Interval (in minutes)</label>
              <div class="slds-form-element__control">
                <input id="interval-time-input" class="slds-input time-input" type="number" min="1" v-model.number="currentResponse.Interval" :disabled="!currentResponse.AllowMultipleReply" @keydown="checkNum($event, currentResponse.Interval)"  @keyup="limitNum(currentResponse.Interval, maxInterval)"/>
              </div>
              <div class="slds-form-element__help" v-show="showError">Up to 525600</div>
            </div>
          </div>

          <div class="slds-form-element form-section">
            <div class="slds-form-element__control">
              <wechat-response
                v-model="autoReply"
                :limit-of-items="3"
                page-name="Agent Status"
                :wechat-account="currentAccountItem"
                :choosers="wechatResponseConfig"
                :disabled="!currentResponse.WeChatAccount.Id"
                :required="true"
                multiple
                enable-reply-mode
              />
            </div>
          </div>
        </div>
        <loading :is-loading="isResponseLoading" custom-style="position: relative; padding : 40px; background-color: white"></loading>
        <footer class="slds-modal__footer">
          <button class="slds-button slds-button_neutral" @click="closeNewResponse">Cancel</button>
          <button class="slds-button slds-button_brand" :disabled="isSaving" @click="saveResponse">Save</button>
        </footer>
      </div>
    </section>
    <div class="slds-backdrop slds-backdrop_open"></div>
  </div>
</template>

<script>
import WechatResponse from '../../components/wechat_response'
import { createResponse, updateResponse, getResponse, checkResponseAccount } from '../../api/charket-settings.js'
import { MessageService } from '../../service'
import HTMLUtil from 'charket-ui/dist/utils/HTMLUtil'
import CheckLimitNum from '../../mixins/checkLimitNum.js'

export default {
  name: 'NewResponse',
  mixins: [CheckLimitNum],
  components: {
    WechatResponse
  },
  props: {
    agentStatusId: {
      type: String,
      default () {
        return ''
      }
    },
    response: {
      type: Object,
      default () {
        return {}
      }
    },
    responseName: {
      type: String,
      default () {
        return ''
      }
    }
  },
  data () {
    return {
      responseTitle: 'New Response',
      currentAccountItem: {},
      currentResponse: {
        Name: '',
        WeChatAccount: {
          Id: '',
          Name: '',
          Type: ''
        },
        IsActive: true,
        AllowMultipleReply: false,
        Interval: null,
        ReplyMode: 'Random',
        MessageIds: []
      },
      isSaving: false,
      isResponseLoading: false,
      isMiniType: false,
      errorToastMsg: '',
      resError: {
        WeChatAccountError: false,
        IntervalError: false
      },
      toastMsgInfo: '',
      maxInterval: 525600,
      toggleStatuses: ['Yes', 'No'],
      isNewOpened: false,
      autoReply: {
        mode: 'Random',
        items: []
      },
      currentInterval: null
    }
  },
  watch: {
    'currentResponse.AllowMultipleReply' () {
      if (!this.currentResponse.AllowMultipleReply) {
        this.currentInterval = this.currentResponse.Interval
        this.currentResponse.Interval = null
        this.resError.IntervalError = false
      } else {
        this.currentResponse.Interval = this.currentInterval
      }
    },
    'autoReply.items' (value) {
      this.currentResponse.MessageIds = value.map((item) => {
        return item.id
      })
    }
  },
  async created () {
    if (this.response.Id) {
      this.isResponseLoading = true
      this.responseTitle = 'Edit Response'
      try {
        this.currentResponse = await getResponse(this.response.Id)
        this.isMiniType = this.currentResponse.WeChatAccount.Type === 'Mini Program'
        this.isNewOpened = this.currentResponse.AllowMultipleReply
        this.currentInterval = this.currentResponse.Interval
        if (this.currentResponse.WeChatAccount.Id) {
          this.currentAccountItem = {
            name: this.currentResponse.WeChatAccount.Name,
            id: this.currentResponse.WeChatAccount.Id,
            type: this.currentResponse.WeChatAccount.Type
          }
        }
        if (this.currentResponse.MessageIds.length > 0) {
          await this.getAllMessages(this.currentResponse.MessageIds)
        }
      } catch (error) {
        this.$bus.emit('error', { content: error.msg, type: 'error' })
      } finally {
        this.isResponseLoading = false
      }
    } else {
      this.responseTitle = 'New Response'
      this.currentResponse.Name = `Response for Agent Status - ${this.responseName}`
    }
  },
  computed: {
    wechatResponseConfig () {
      if (this.currentResponse.WeChatAccount.Id && this.isMiniType) {
        return ['Text Message']
      }

      return ['Text Message', 'Image', 'Structured Message', 'Voice Message', 'Video']
    }
  },
  methods: {
    async getAllMessages (messageIds) {
      try {
        const data = await MessageService.getMessagesByIds(messageIds)
        this.autoReply.items = data
      } catch (error) {
        this.$handleError(error)
      }
    },
    toggleMultipleReply (status) {
      this.currentResponse.AllowMultipleReply = status
    },
    closeNewResponse () {
      this.$emit('close-response-modal', false)
    },
    async checkAccount () {
      try {
        await checkResponseAccount(this.agentStatusId, this.currentResponse.WeChatAccount.Id)
      } catch (error) {
        if (error.type === 'serve_error') {
          let errorRes = JSON.parse(HTMLUtil.unescape(error.msg))
          if (this.currentResponse.WeChatAccount.Id === errorRes.WeChatAccountId) {
            this.errorToastMsg = errorRes.Error
            this.resError.WeChatAccountError = true
          }
        } else {
          this.$bus.emit('error', { content: error.msg, type: 'error' })
        }
      }
    },
    async onSelect (event) {
      this.currentResponse.WeChatAccount.Id = event ? event.Id : ''
      if (this.currentResponse.WeChatAccount.Id) {
        this.resError.WeChatAccountError = false
        await this.checkAccount()
        this.currentAccountItem = {
          name: this.currentResponse.WeChatAccount.Name,
          id: this.currentResponse.WeChatAccount.Id,
          type: this.currentResponse.WeChatAccount.Type
        }
      }
      this.isMiniType = event && event.Type__c === 'Mini Program'
    },
    onRemove () {
      this.isMiniType = false
      this.currentResponse.WeChatAccount.Id = ''
      this.currentResponse.MessageIds = []
      this.autoReply.items = []
      this.errorToastMsg = ''
      this.resError.WeChatAccountError = false
      this.currentAccountItem = {}
    },
    checkSave (res) {
      let requiredObj = []
      let canSave
      let checkObj = ['WeChatAccount']
      if (res.AllowMultipleReply) {
        checkObj.push('Interval')
      }
      for (const key of checkObj) {
        if (!res[key] || (key === 'WeChatAccount' && !res[key].Id)) {
          requiredObj.push(key)
          this.resError[`${key}Error`] = true
          const watchError = this.$watch(() => {
            return res[key]
          }, function (newVal, oldVal) {
            this.resError[`${key}Error`] = false
            watchError()
          })
        }
      }
      if (!(res.MessageIds.length > 0 || res.Skill)) {
        requiredObj.push('Responses')
      }
      if (requiredObj.length > 0 || this.errorToastMsg || (res.MessageIds.length <= 0 && !res.Skill) || this.showError) {
        canSave = false
      } else {
        canSave = true
      }
      if (requiredObj.length > 0) {
        let requiredStr = requiredObj.join(', ')
        this.toastMsgInfo = `These required fields must be completed: ${requiredStr}`
      } else {
        this.toastMsgInfo = ''
      }
      return canSave
    },
    async saveResponse () {
      let canSave = this.checkSave(this.currentResponse)
      if (canSave) {
        this.isSaving = true
        if (this.currentResponse.Id) {
          try {
            await updateResponse(JSON.stringify(this.currentResponse))
            this.isSaving = false
            this.$bus.emit('success', { content: 'Response was saved.', type: 'success' })
            this.$emit('close-response-modal', true)
          } catch (error) {
            this.isSaving = false
            this.$bus.emit('error', { content: error.msg, type: 'error' })
          }
        } else {
          try {
            await createResponse(this.agentStatusId, JSON.stringify(this.currentResponse))
            this.isSaving = false
            this.$bus.emit('success', { content: 'Response was created.', type: 'success' })
            this.$emit('close-response-modal', true)
          } catch (error) {
            this.isSaving = false
            this.$bus.emit('error', { content: error.msg, type: 'error' })
          }
        }
      } else {
        const content = document.getElementById('response-modal-content')
        content.scrollTop = 0
      }
    }
  }
}
</script>

<style lang="scss" scoped>
  #response-modal-content {
    scroll-behavior: smooth;
    .form-section {
      margin-bottom: 20px;
      .red-color {
        color: #c23934;
      }
    }
    .response-disabled-cover {
      position: absolute;
      top: 25px;
      left: 0;
      right: 0;
      height: 40px;
      background-color: rgba(255, 255, 255, 0.3);
      z-index: 1000;
    }

    .form-title {
      span {
        color: #c23934;
      }
    }

    .left-section {
      display: inline-block;
      width: 55%;
      margin-right: 25px;
    }
    .right-section {
      display: inline-block;
      .cursor-label {
        cursor: pointer;
      }
    }

    .time-input {
      width: 30%;
    }

    .error-info {
      font-size: 12px;
      color: #c23934;
      margin-top: -20px;
    }

    .toast-container {
      display: inline-block;
      width: 100%;
      .notify-toast-error {
        border-radius: 0.25rem;
        padding: 0.75rem 1.5rem;
      }
    }
  }
</style>
