<template>
  <div>
    <button class="slds-button slds-button_icon slds-button_icon-border-filled slds-button_icon-x-small" aria-haspopup="true" tabindex="-1" @click="openActionBox($event)">
      <icon icon-class-name="slds-button__icon slds-button__icon_hint slds-button__icon_small" name="down">
      </icon>
    </button>
    <div class="action" :style="{left: position.left, top: position.top}" v-show="showAction">
      <ul>
        <li class="action-item" v-for="(action, index) in displayActions" :key="index" @click="selectedAction(action)">{{ action }}</li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ActionMenu',
  props: {
    actions: {
      type: Array,
      default () {
        return []
      }
    },
    item: {
      type: Object,
      default () {
        return {}
      }
    },
    concealedActions: {
      type: Array,
      default () {
        return []
      }
    }
  },
  data () {
    return {
      windowEventHandler: null,
      windowRedrawListenerHandler: null,
      position: {
        left: '',
        top: ''
      },
      showAction: false,
      targetEle: null
    }
  },
  computed: {
    displayActions () {
      return this.actions.filter(action => !this.concealedActions.includes(action))
    }
  },
  created () {
    this.windowEventHandler = (e) => {
      if (!this.$el || this.$el.contains(e.target)) {
        return null
      }
      this.targetEle = null
      this.showAction = false
    }
    window.addEventListener('click', this.windowEventHandler)
    window.addEventListener('dragstart', this.windowEventHandler)
  },
  mounted () {
    this.$nextTick(function () {
      this.windowRedrawListenerHandler = () => {
        if (!this.targetEle) {
          return null
        }
        this.setActionMenuPosition(this.targetEle)
      }
      window.addEventListener('resize', this.windowRedrawListenerHandler)
      // 兼容IE跟Chrome的滚动事件
      window.addEventListener('mousewheel', this.windowRedrawListenerHandler)
      // 兼容Firefox的滚动事件
      window.addEventListener('DOMMouseScroll', this.windowRedrawListenerHandler)
    })
  },
  beforeDestroy () {
    window.removeEventListener('click', this.windowEventHandler)
    window.removeEventListener('dragstart', this.windowEventHandler)
    window.removeEventListener('resize', this.windowRedrawListenerHandler)
    window.removeEventListener('mousewheel', this.windowRedrawListenerHandler)
    window.removeEventListener('DOMMouseScroll', this.windowRedrawListenerHandler)
  },
  methods: {
    selectedAction (action) {
      this.$emit(`${action.toLowerCase()}`, this.item)
    },
    openActionBox (event) {
      this.targetEle = event.target
      this.setActionMenuPosition(this.targetEle)
      this.showAction = true
    },
    setActionMenuPosition (targetEle) {
      let targetRect = targetEle.getBoundingClientRect()
      // 97为action div的宽度，20为action button的宽度，10为action button的一半高度，2和12为指定的间隔距离
      this.position.left = `${targetRect.left - 97 + 20 - 2}px`
      this.position.top = `${targetRect.top + 10 + 12}px`
    }
  }
}
</script>

<style lang="scss" scoped>
  .action {
    color: #0070D2;
    width: 97px;
    background: white;
    position: fixed;
    border: 1px solid #D8DDE6;
    z-index: 10;
    font-size: 12px;
    border-radius: 4px;
  }

  .action-item {
    width: 100%;
    padding-left: 10px;
    padding-top: 5px;
    padding-bottom: 5px;
    margin-top: 5px;
  }

  .action-item:hover {
    cursor: pointer;
    background-color: rgb(244, 246, 249);
  }

  ul:last-child {
    margin-bottom: 5px;
  }
</style>
