import { CharketSettingsTransport } from '../transport'

const transport = new CharketSettingsTransport()

class PardotService {
  static async getSettings () {
    const {
      IsPardotEnabled: isPardotEnabled,
      IsEditable: isEditable,
      Message: message
    } = await transport.getPardotSettings()
    return { isPardotEnabled, isEditable, message }
  }

  static async saveSettings (isPardotEnabled) {
    const data = {
      IsPardotEnabled: isPardotEnabled
    }
    return transport.savePardotSettings(data)
  }

  static async getFormHandler () {
    const { EndpointUrl } = await transport.getPardotFormHandler()
    return EndpointUrl
  }

  static async saveFormHandler (endpointURL) {
    const data = {
      EndpointUrl: endpointURL
    }
    return transport.savePardotFormHandler(data)
  }
}

export default PardotService
