          <div class="slds-form-element__control">
            <div class="slds-checkbox">
              <input type="checkbox" name="options" id="checkbox1" disabled :checked="agentStatus.IsAcceptsChats"/>
              <label class="slds-checkbox__label" for="checkbox1">
                <span class="slds-checkbox_faux"></span>
              </label>
            </div>
          </div>
        </div>
      </div>
      <div class="field-section down-section">
        <div class="slds-form-element left-field-checkbox">
          <label class="slds-form-element__label">Active</label>
          <div class="slds-form-element__control">
            <div class="slds-checkbox">
              <input type="checkbox" name="options" id="checkbox2" disabled :checked="agentStatus.IsActive"/>
              <label class="slds-checkbox__label" for="checkbox2">
                <span class="slds-checkbox_faux"></span>
              </label>
            </div>
          </div>
        </div>
        <div class="slds-form-element right-field">
          <label class="slds-form-element__label">Offline Default Status</label>
          <div class="slds-form-element__control">
            <div class="slds-checkbox">
              <input type="checkbox" name="options" id="checkbox3" disabled :checked="agentStatus.IsOfflineDefaultStatus"/>
              <label class="slds-checkbox__label" for="checkbox3">
                <span class="slds-checkbox_faux"></span>
              </label>
            </div>
          </div>
        </div>
      </div>
      <div class="field-section down-section">
        <div class="slds-form-element left-field non-border">
          <label class="slds-form-element__label">Created By</label>
          <div class="slds-text-body_regular">{{ agentStatus.CreatedBy }}, {{ agentStatus.CreatedDate }}</div>
        </div>
        <div class="slds-form-element right-field">
          <label class="slds-form-element__label">Non-Overridable By System</label>
          <tooltip
            info-style="top: 0; left: 152px;"
            content="An agent choosing this status will remain as the status without being affected by system default behaviors. Such status is usually used by the Charket mobile app.">
          </tooltip>
          <div class="slds-form-element__control">
            <div class="slds-checkbox">
              <input type="checkbox" name="options" id="checkbox4" disabled :checked="agentStatus.IsNonoverridableBySystem"/>
              <label class="slds-checkbox__label" for="checkbox4">
                <span class="slds-checkbox_faux"></span>
              </label>
            </div>
          </div>
        </div>
      </div>
      <div class="field-section down-section">
        <div class="slds-form-element left-field non-border">
        </div>
        <div class="slds-form-element right-field non-border">
          <label class="slds-form-element__label">Last Modified By</label>
          <div class="slds-text-body_regular">{{ agentStatus.LastModifiedBy }}, {{ agentStatus.LastModifiedDate }}</div>
        </div>
      </div>
