              <th data-label="WECHAT ACCOUNT" class="first-header-label" scope="row">
                <div class="slds-truncate"><a :href="'/' + response.Id" :title="response.WeChatAccount.Name" target="_blank">{{ response.WeChatAccount.Name }}</a></div>
              </th>
              <td data-label="ACTIVE">
                <div class="slds-form-element">
                  <div class="slds-form-element__control">
                    <div class="slds-checkbox">
                      <input type="checkbox" name="options" id="checkbox5" disabled :checked="response.IsActive"/>
                      <label class="slds-checkbox__label" for="checkbox5">
                        <span class="slds-checkbox_faux"></span>
                      </label>
                    </div>
                  </div>
                </div>
              </td>
              <td data-label="INTERVAL (MINUTES)">
                <div class="slds-truncate">{{ response.Interval }}</div>
              </td>
              <td data-label="MESSAGES">
                <div class="slds-truncate">{{ response.Skill && response.Skill.Id ? 1 : response.MessageIds.length }}</div>
              </td>
              <td data-label="REPLY MODE">
                <div class="slds-truncate">{{ response.ReplyMode }}</div>
              </td>
              <td data-label="LAST MODIFIED BY">
                <div class="slds-truncate" :title="`${response.LastModifiedBy}, ${response.LastModifiedDate}`">{{ response.LastModifiedBy }}, {{ response.LastModifiedDate }}</div>
              </td>
              <td data-label="Action">
                <action-menu :actions="responseActions" :item="response" @edit="editResponseModal" @delete="openDeleteModal">
                </action-menu>
              </td>
            </tr>
          </tbody>
        </table>
        <div class="no-responses" v-else key="responses-list">
          <div class="slds-text-body_regular">No Responses</div>
        </div>
      </div>
      <new-response :response-name="agentStatus.Name" :response="editResponse" :agent-status-id="agentStatusId" @close-response-modal="closeResponseModal" v-if="showResponse"></new-response>
    </div>
  </right-page>
</template>

<script>
import NewAgentStatus from './NewAgentStatus'
import NewResponse from './NewResponse.vue'
import { getAgentStatus, deleteResponse, getResponses } from '../../api/charket-settings'

export default {
  name: 'AgentStatusDetail',
  components: {
    NewAgentStatus,
    NewResponse
  },
  data () {
    return {
      responses: [],
      responseActions: ['Edit', 'Delete'],
      showResponse: false,
      agentStatusId: '',
      editResponse: {},
      hasSecondContent: true,
      agentStatus: {},
      selectedAgentStatus: {},
      selectedResponse: {}
    }
  },
  async created () {
    this.agentStatusId = this.$route.params.statusId
    this.fetchResponses()
  },
  methods: {
    newResponseModal () {
      this.editResponse = {}
      this.showResponse = true
    },
    editResponseModal (item) {
      this.editResponse = item
      this.showResponse = true
    },
    closeResponseModal (needToRefresh) {
      if (needToRefresh) {
        this.fetchResponses()
      }
      this.showResponse = false
    },
    async fetchResponses () {
      try {
        this.$bus.emit('show-loading')
        const [agentStatusData, responses] = await Promise.all([getAgentStatus(this.agentStatusId), getResponses(this.agentStatusId)])
        this.agentStatus.Id = agentStatusData.Id
        this.agentStatus.Order = agentStatusData.Order
        this.agentStatus.Name = agentStatusData.Name
        this.agentStatus.IsActive = agentStatusData.IsActive
        this.agentStatus.IsOfflineDefaultStatus = agentStatusData.IsOfflineDefaultStatus
        this.agentStatus.IsAcceptsChats = agentStatusData.IsAcceptsChats
        this.agentStatus.IsNonoverridableBySystem = agentStatusData.IsNonoverridableBySystem
        this.agentStatus.CreatedBy = agentStatusData.CreatedBy
        this.agentStatus.CreatedDate = agentStatusData.CreatedDate
        this.agentStatus.LastModifiedBy = agentStatusData.LastModifiedBy
        this.agentStatus.LastModifiedDate = agentStatusData.LastModifiedDate
        this.responses = responses
      } catch (error) {
        this.$bus.emit('error', { content: error.msg, type: 'error' })
      } finally {
        this.$bus.emit('hide-loading')
      }
    },
    editAgentStatus () {
      this.$refs.EditAgentStatus.open()
      this.selectedAgentStatus = this.agentStatus
    },
    cancelEditAgentStatus () {
      this.selectedAgentStatus = {}
    },
    saveAgentStatus () {
      this.selectedAgentStatus = {}
      this.fetchResponses()
    },
    openDeleteModal (item) {
      this.selectedResponse = item
      const confirm = this.$confirm({
        title: 'Delete Response',
        content: 'Are you sure want to delete this response?',
        ok: 'OK'
      })

      confirm.$on('ok', async () => {
        if (!this.selectedResponse.Id) {
          return null
        }
        await this.deleteObj(this.selectedResponse, confirm)
      })
    },
    async deleteObj (response, confirm) {
      try {
        await deleteResponse(response.Id)
        this.$bus.emit('success', { content: 'Response was deleted.', type: 'success' })
        confirm.close()
        this.fetchResponses()
      } catch (error) {
        this.$bus.emit('error', { content: error.msg, type: 'error' })
      } finally {
        confirm.resume()
      }
    },
    goToDefaultPage () {
      this.$router.push({ path: '/' })
    },
    goToAgentStatusList () {
      this.$router.push({ name: 'AgentStatusesSettings' })
    }
  }
}
</script>

