import Transport from './Transport'

const ACTION_BASE_NAME = 'CharketSettingsController'

class CharketSettingsTransport extends Transport {
  constructor () {
    super(ACTION_BASE_NAME)
  }

  getSFDCServerIPAddress () {
    return this.invoke('getSFDCServerIPAddress')
  }

  getPardotSettings () {
    return this.invoke('getPardotSettings')
  }

  savePardotSettings (pardotSettings) {
    return this.invoke('savePardotSettings', [
      JSON.stringify(pardotSettings)
    ])
  }

  getPardotFormHandler () {
    return this.invoke('getPardotFormHandler')
  }

  savePardotFormHandler (formHandler) {
    return this.invoke('savePardotFormHandler', [
      JSON.stringify(formHandler)
    ])
  }

  getCustomHandlers (type, executionOrder) {
    return this.invoke('getCustomHandlers', [
      type,
      executionOrder
    ])
  }

  saveCustomHandlers (type, executionOrder, handlers) {
    return this.invoke('saveCustomHandlers', [
      type,
      executionOrder,
      JSON.stringify(handlers)
    ])
  }

  getCharketSmsSettings () {
    return this.invoke('getCharketSmsSettings', [])
  }

  saveCharketSmsSettings (settingsJSON) {
    return this.invoke('saveCharketSmsSettings', [
      JSON.stringify(settingsJSON)
    ])
  }

  getVerificationCodeSettings () {
    return this.invoke('getVerificationCodeSettings', [])
  }

  saveVerificationCodeSettings (setingsJSON) {
    this.invoke('saveVerificationCodeSettings', [
      JSON.stringify(setingsJSON)
    ])
  }

  getChatDataSettings () {
    return this.invoke('getChatDataSettings')
  }

  saveChatDataSettings (settings) {
    return this.invoke('saveChatDataSettings', [
      JSON.stringify(settings)
    ])
  }
}
export default CharketSettingsTransport
