<template>
  <ld-modal
    :title="modalTitle"
    width="90%"
    @close="onModalClose"
    ref="modal"
  >
    <template slot="content">
      <div class="slds-lookup slds-scrollable_y">
        <div class="ld-lookup-typeahead">
          <typeahead
            :ld-keyword="keyword"
            :ld-metadatas="ldMetadata"
            :ld-typeahead="ldSearch"
            :ld-search-items="ldSearchItems"
            :ld-show-dropdwon="showDropdown"
            @search="search($event)"
            @select="select($event)">
          </typeahead>
        </div>
        <div class="ld-lookup-result-header" tabindex="-1">
          <div class="slds-page-header page-header-space">
            <div class="slds-grid slds-grid_vertical-align-center">
              <div class="slds-col">
                <h2 class="slds-m-bottom_xx-small">
                  <div class="slds-text-heading_medium slds-text-color_default">
                    {{ldMetadata.labelPural}}
                  </div>
                </h2>
                <div class="slds-text-body_small slds-text-color_weak">
                  <div>
                    <p>{{message}}</p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="ld-lookup-table">
          <div v-show="showLoading" class="slds-spinner_container">
            <div class="slds-spinner_brand slds-spinner slds-spinner_medium" role="alert">
              <span class="slds-assistive-text">Loading</span>
              <div class="slds-spinner__dot-a"></div>
              <div class="slds-spinner__dot-b"></div>
            </div>
          </div>
          <table
            v-show="items.length > 0"
            class="slds-table slds-table_bordered slds-table_cell-buffer slds-no-row-hover lookup-table"
            role="listbox"
          >
            <thead>
              <tr class="slds-line-height_reset" style="height: 2rem;">
                <th
                  v-for="(field, index) in ldMetadata.fields"
                  :key="index"
                  scope="col"
                >
                  <div class="slds-truncate">{{ field.label }}</div>
                </th>
              </tr>
            </thead>
            <tbody>
              <tr
                class="ld-lookup-result-row"
                v-for="(item, index) in items"
                :key="index"
              >
                <td
                  scope="row"
                  v-for="(field, index) in ldMetadata.fields"
                  :key="index"
                >
                  <a
                    href="javascript:void(0);"
                    role="option"
                    v-if="index === 0"
                    @click="select(item)"
                  >
                    <div class="slds-truncate">
                      {{ item[getCamelCaseKey(field.name)] }}
                    </div>
                  </a>
                  <div
                    class="slds-truncate"
                    v-if="index !== 0"
                  >
                    {{ item[getCamelCaseKey(field.name)] }}
                  </div>
                </td>
              </tr>
            </tbody>
          </table>
          <div v-show="items.length === 0" class="slds-p-around_large slds-text-body_regular">
            <div class="">{{`No results for "${keyword}" in ${ldMetadata.label}`}}</div>
          </div>
        </div>
      </div>
    </template>
    <template slot="footer">
      <div>
        <button class="slds-button slds-button_neutral" aria-live="off" type="button" @click="onClickCancelBtn()">
          <span class="" dir="ltr">Cancel</span>
        </button>
      </div>
    </template>
  </ld-modal>
</template>

<script>
import ErrorToast from './ErrorToast.vue'
import Typeahead from './Typeahead.vue'
import SearchMixin from './search-mixin.js'
import { LdModal } from 'charket-ui/dist/ld-modal.js'

export default {
  mixins: [SearchMixin],
  components: {
    ErrorToast,
    Typeahead,
    LdModal
  },
  props: {
    ldMetadata: {
      type: Object,
      default () {
        return {
          fields: []
        }
      }
    },
    ldKeyword: {
      type: String,
      default () {
        return ''
      }
    },
    ldShow: {
      type: Number,
      default () {
        return -1
      }
    },
    ldSearch: {
      type: Function
    },
    ldSearchItems: {
      type: Function
    }
  },
  data () {
    return {
      showLoading: false,
      keyword: '',
      message: '',
      items: [],
      fields: [],
      linkField: {},
      showDropdown: false
    }
  },
  computed: {
    modalTitle () {
      return `${this.ldMetadata.label} Results`
    }
  },
  watch: {
    ldShow () {
      this.linkField = this.ldMetadata.linkField
      this.items = []
      this.message = ''
      this.$refs.modal.open()
      this.search({
        keyword: this.ldKeyword,
        metadata: this.ldMetadata
      })
    }
  },
  methods: {
    getCamelCaseKey (key) {
      return key.substring(0, 1).toLowerCase() + key.substring(1)
    },
    async search ({ keyword, metadata }) {
      this.keyword = keyword
      this.showLoading = true
      try {
        const { columns, records } = await this.searchWithFields(keyword.trim(), metadata)
        this.ldMetadata.fields = columns
        this.items = records
        if (this.items.length === 1) {
          this.message = `1 Result`
        } else if (this.items.length > 1) {
          this.message = `${this.items.length} Results • Sorted by relevancy`
        } else {
          this.message = `0 Results`
        }
      } catch (error) {
        this.$refs.modal.showError(error.message)
      } finally {
        this.showLoading = false
      }
    },
    select (item) {
      this.$emit('select', item)
      this.keyword = ''
      this.$refs.modal.close()
    },
    onModalClose () {
      this.keyword = ''
    },
    onClickCancelBtn () {
      this.keyword = ''
      this.$refs.modal.close()
    }
  }
}
</script>

