class WeChatAccountConfiguration {
  constructor (data) {
    Object.assign(this, data)
    this.ProgressInfo = null
    this.Progress = this.IsSetupDone ? 100 : -1
  }

  get lastModifiedInfo () {
    return `${this.LastModifiedBy}, ${this.LastModifiedDate}`
  }

  setProgressInfo (progressInfo) {
    this.ProgressInfo = progressInfo
    this.Progress = progressInfo.IsSetupDone
      ? 100
      : ((progressInfo.CurrentSetupStep - 1) / progressInfo.TotalSteps) * 100
  }
}

export default WeChatAccountConfiguration
