<template>
  <div id="input-box" class="slds-form-element" @click.stop="">
    <label class="slds-form-element__label"><span class="red-color">*</span> WeChat Account</label>
    <div class="slds-form-element__control">
      <div class="slds-combobox_container">
        <div class="slds-combobox slds-dropdown-trigger slds-dropdown-trigger_click" aria-expanded="false" aria-haspopup="listbox" role="combobox">
          <div class="slds-combobox__form-element slds-input-has-icon" :class="{'slds-input-has-icon_right': !currentItem.id, 'slds-input-has-icon_left-right slds-pill_container selected-item': currentItem.id}" role="none">
            <span class="slds-icon_container slds-icon-standard-wechat-account slds-combobox__input-entity-icon icon-span" title="WeChat Account" v-show="currentItem.id">
              <icon
                name="wechat_account_miniprogram"
                :icon-style="iconStyle"
                icon-class-name="slds-icon slds-icon_small"
                v-if="currentItem.type === 'Mini Program'"
              >
              </icon>
              <icon
                name="wechat_account_service"
                :icon-style="iconStyle"
                icon-class-name="slds-icon slds-icon_small"
                v-if="currentItem.type === 'Service Account'"
              >
              </icon>
              <icon
                name="wechat_account_subscription"
                :icon-style="iconStyle"
                icon-class-name="slds-icon slds-icon_small"
                v-if="currentItem.type === 'Subscription Account'"
              >
              </icon>
              <span class="slds-assistive-text">WeChat Account</span>
            </span>
            <input class="slds-input slds-combobox__input"
                   :class="{'slds-combobox__input-value': currentItem.id, 'is-danger': isDanger}"
                   aria-autocomplete="list"
                   aria-controls="listbox-id-1"
                   autocomplete="off"
                   role="textbox"
                   type="text"
                   placeholder="Search WeChat Accounts..."
                   @keydown.enter="enterKey()"
                   @keyup="upKey($event)"
                   @focus="focusKey($event)"
                   :disabled="currentItem.id"
                   v-model="searchKey"
            />
            <span class="slds-icon_container slds-icon-utility-search slds-input__icon slds-input__icon_right" v-show="!currentItem.id">
              <icon name="search" icon-class-name="slds-icon slds-icon slds-icon_x-small slds-icon-text-default">
              </icon>
            </span>
            <button class="slds-button slds-button_icon slds-input__icon slds-input__icon_right" title="Remove selected option" @click="removeCurrent" v-show="currentItem.id">
              <icon name="close" icon-class-name="slds-button__icon">
              </icon>
              <span class="slds-assistive-text">Remove selected option</span>
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getSizeOfUTF8Text } from 'charket-ui/dist/utils/rich-text'

export default {
  name: 'LookupInputBox',
  props: {
    current: {
      type: Object,
      default () {
        return {}
      }
    },
    keyword: {
      type: String,
      default () {
        return ''
      }
    },
    isDanger: {
      type: Boolean,
      default () {
        return false
      }
    }
  },
  data () {
    return {
      currentItem: {},
      searchKey: '',
      inputTarget: null,
      windowListenerHandler: null,
      iconStyle: { width: '20px', height: '20px', padding: '3px' }
    }
  },
  computed: {
    textSize () {
      return this.searchKey
        ? getSizeOfUTF8Text(this.searchKey.trim())
        : 0
    }
  },
  watch: {
    'current.id' () {
      this.currentItem = JSON.parse(JSON.stringify(this.current))
      this.searchKey = this.currentItem.name
    }
  },
  created () {
    this.currentItem = JSON.parse(JSON.stringify(this.current))
    this.searchKey = this.currentItem.name
    if (this.keyword) {
      this.searchKey = this.keyword
    }
  },
  mounted () {
    this.$bus.on('close-dropdown', (account) => {
      this.inputTarget = null
      if (account) {
        this.currentItem = { name: account.Name, id: account.Id, type: account.Type__c }
        this.searchKey = account.Name
      }
    })
    this.$nextTick(function () {
      this.windowListenerHandler = () => {
        if (this.inputTarget) {
          let position = this.calculatePosition(this.inputTarget, this.inputTarget.getBoundingClientRect())
          this.$bus.emit('open-dropdown', { type: 'orientation', keyword: this.searchKey, position: position })
        }
      }
      window.addEventListener('resize', this.windowListenerHandler)
      // 兼容IE跟Chrome的滚动事件
      window.addEventListener('mousewheel', this.windowListenerHandler)
      // 兼容Firefox的滚动事件
      window.addEventListener('DOMMouseScroll', this.windowListenerHandler)
    })
  },
  beforeDestroy () {
    this.$bus.off('close-dropdown')
    window.removeEventListener('resize', this.windowListenerHandler)
    window.removeEventListener('mousewheel', this.windowListenerHandler)
    window.removeEventListener('DOMMouseScroll', this.windowListenerHandler)
  },
  methods: {
    enterKey () {
      if (this.textSize >= 2) {
        this.inputTarget = null
        this.$bus.emit('detail-close-dropdown')
        this.$bus.emit('open-detail-modal', this.searchKey)
      }
    },
    upKey (event) {
      if (event.code !== 'Enter') {
        this.inputTarget = event.target
        let position = this.calculatePosition(this.inputTarget, this.inputTarget.getBoundingClientRect())
        if (this.textSize < 2) {
          this.$bus.emit('open-dropdown', { type: 'filter', keyword: this.searchKey, position: position })
        } else {
          this.$bus.emit('open-dropdown', { type: 'search', keyword: this.searchKey, position: position })
        }
      }
    },
    focusKey (event) {
      this.inputTarget = event.target
      let position = this.calculatePosition(this.inputTarget, this.inputTarget.getBoundingClientRect())
      this.$bus.emit('open-dropdown', { type: 'search', keyword: this.searchKey, position: position })
    },
    removeCurrent () {
      this.currentItem = {}
      this.searchKey = ''
      this.$emit('remove')
    },
    calculatePosition (targetEle, targetRect) {
      let position = {
        width: '',
        left: '',
        top: ''
      }
      position.width = `${targetEle.clientWidth}px`
      position.left = `${targetRect.left}px`
      // 32 is the input client height
      position.top = `${targetRect.top + 32}px`
      return position
    }
  }
}
</script>

<style lang="scss"  scoped>
  #input-box {
    .red-color {
      color: #c23934;
    }
    .slds-input {
      cursor: auto;
      background-color: white;
    }
    .icon-span {
      margin-left: 2px;
    }
    .is-danger {
      border-color: rgb(194, 57, 52);
      box-shadow: rgb(194, 57, 52) 0 0 0 1px inset;
      background-clip: padding-box;
    }
    .selected-item {
      padding: 3px;
    }
    .slds-icon-standard-wechat-account {
      background-color: #54698d;
    }
  }
</style>
