import { getDataBySql } from '../../api/message'
import config from '../../config'

const ns = config.sfdc_namespace || ''
let wechatAccount = 'WeChatAccount__c'
let accountType = 'Type__c'
if (ns) {
  wechatAccount = `${ns}__WeChatAccount__c`
  accountType = `${ns}__Type__c`
}

export default {
  data () {
    return {
      wechatAccounts: []
    }
  },
  methods: {
    async fetchWeChatAccounts (searchKey) {
      try {
        const sql = `select Id, Name, ${accountType} from ${wechatAccount} where Name like '%${searchKey}%' order by Name`
        this.wechatAccounts = await getDataBySql(sql)
      } catch (error) {
        this.$bus.emit('error', { content: error.msg, type: 'error' })
      }
    }
  }
}
