import { WeChatAccountTransport } from '../transport'
import { WeChatAccountConfiguration } from '../model'

const transport = new WeChatAccountTransport()

class WeChatAccountConfigurationService {
  static async getConfigurations () {
    const result = await transport.getAllWeChatAccounts()

    return result.map((data) => new WeChatAccountConfiguration(data))
  }

  static delete (id) {
    return transport.deleteWeChatAccount(id)
  }

  static async refreshProgressInfo (configuration) {
    const progressInfo = await transport.checkSetupStep(configuration.Id)
    configuration.setProgressInfo(progressInfo)
  }

  static syncWeChatData (id, options) {
    const { syncFollower, syncMenu, syncAutoReplyRules } = options
    const data = []
    if (syncFollower) {
      data.push('Follower')
    }

    if (syncMenu) {
      data.push('Menu')
    }

    if (syncAutoReplyRules) {
      data.push('AutoReplyRule')
    }

    return transport.syncWeChatData(id, data)
  }

  static async getConfiguration (id) {
    const data = await transport.getWeChatAccountSettings(id)

    return new WeChatAccountConfiguration(data)
  }

  static getWeChatAccountTypes () {
    return transport.getTypes()
  }

  static getSites () {
    return transport.getSites()
  }

  static generateToken () {
    return transport.generateToken()
  }

  static create (data) {
    return transport.createWeChatAccount(data)
  }

  static update (data) {
    return transport.updateWeChatAccount(data)
  }

  static async uploadQRCodeImage (id, qrcode) {
    const attachment = {
      Name: qrcode.name,
      ContentType: qrcode.type,
      Body: qrcode.content
    }
    const { AttachmentId: qrcodeImageId } = await transport.saveQRCode(id, attachment)

    return qrcodeImageId
  }

  static async getCurrentStep (id) {
    const { CurrentSetupStep } = await transport.checkSetupStep(id)

    return CurrentSetupStep
  }

  static checkSyncDataJobStatus (id) {
    return transport.checkSyncDataJobStatus(id)
  }

  static async getLinkWeChatFollowerToOptions () {
    const options = await transport.getLinkWeChatFollowerTo()
    return options.map((option) => {
      return {
        label: option.Label === 'None'
          ? '--None--'
          : option.Label,
        value: option.Label,
        recordTypes: option.RecordTypes.map((data) => {
          return {
            label: data.Name,
            value: data.Id
          }
        })
      }
    })
  }

  static updateLinkWeChatFollowerTo (id, linkWeChatFollowerTo, linkWeChatFollowerToRecordTypeId, isIdentifyWeChatFollowersByUnionIdsEnabled) {
    return transport.updateLinkWeChatFollowerTo(id, linkWeChatFollowerTo, linkWeChatFollowerToRecordTypeId, isIdentifyWeChatFollowersByUnionIdsEnabled)
  }

  static async getIntegrationStatus (id) {
    const {
      IsAutoRepliesIntegrated: autoReply,
      IsMenuIntegrated: menu
    } = await transport.checkAutoRepliesAndMenuDataIntegrity(id)
    return { autoReply, menu }
  }

  static async getActivatedQRCodeAttachmentId (id) {
    const { AttachmentId } = await transport.generateActivatedQRCode(id)
    return AttachmentId
  }

  static async isSetupDone (id) {
    const { IsSetupDone } = await transport.checkSetupDone(id)
    return IsSetupDone
  }

  static async getLinkMethods () {
    const linkMethods = await transport.getLinkMethods()
    return linkMethods
  }
}

export default WeChatAccountConfigurationService
