let instance = null

const CHARKET_NAMESPACE = `${process.ns}` === 'undefined' ? '' : `${process.ns}`

class SFEnvironment {
  static get current () {
    if (!instance) {
      instance = new SFEnvironment()
    }

    return instance
  }

  get charketNamespace () {
    return CHARKET_NAMESPACE
  }
}

export default SFEnvironment
