import Transport from './Transport'

const ACTION_BASE_NAME = 'DatabaseRemoteActions'

class QueryTransport extends Transport {
  constructor () {
    super(ACTION_BASE_NAME)
  }

  getRecentlyViewedRecords (objectType, condition) {
    return this.invoke('getRecentlyViewedRecords', [
      objectType,
      condition
    ])
  }

  searchRecords (searchKey, objectType, condition, orderBy) {
    return this.invoke('searchRecords', [
      searchKey,
      objectType,
      condition,
      orderBy
    ])
  }

  searchRecordsWithLayoutFields (searchKey, objectType, condition, orderBy) {
    return this.invoke('searchRecordsWithLayoutFields', [
      searchKey,
      objectType,
      condition,
      orderBy
    ])
  }
}

export default QueryTransport
