<template>
  <div class="slds-form-element">
    <div class="slds-form-element__control slds-input-has-icon slds-input-has-icon_left-right">
      <icon name="search" icon-class-name="slds-icon slds-input__icon slds-input__icon_left slds-icon-text-default">
      </icon>
      <input
        :placeholder="placeholder"
        class="slds-input"
        type="text"
        @keyup.enter="search"
        @input="input"
        @focus="focus"
        v-model="value"/>
      <button class="slds-button slds-button_icon slds-input__icon slds-input__icon_right" @click="clear" v-show="value">
        <icon name="close" icon-class-name="slds-button__icon slds-icon-text-light"></icon>
      </button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'SearchInput',
  props: {
    placeholder: {
      type: String,
      default () {
        return 'Search this list...'
      }
    }
  },
  data () {
    return {
      value: ''
    }
  },
  methods: {
    search () {
      this.$emit('search', this.value.trim())
    },
    clear () {
      this.value = ''
      this.$emit('clear')
    },
    focus () {
      this.$emit('focus')
    },
    input () {
      this.$emit('input', this.value.trim())
    }
  }
}
</script>

<style lang="scss">
.slds-input-has-icon {
  .slds-input__icon {
    z-index: 0 !important;
  }
}
</style>
