<template>
  <div class="slds-form-element" @click.stop="">
    <div class="slds-form-element__control">
      <div class="slds-combobox_container">
        <div class="slds-combobox slds-dropdown-trigger slds-dropdown-trigger_click slds-combobox-picklist" aria-expanded="false" aria-haspopup="listbox" role="combobox">
          <div class="slds-combobox__form-element slds-input-has-icon slds-input-has-icon_right" role="none" @click="openDropdown($event)">
            <input class="slds-input slds-combobox__input"
                   type="text"
                   placeholder="Select an Option"
                   readonly
                   :value="current.Name"
            />
            <span class="slds-icon_container slds-icon-utility-down slds-input__icon slds-input__icon_right">
              <icon name="down" icon-class-name="slds-icon slds-icon slds-icon_x-small slds-icon-text-default">
              </icon>
            </span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'PicklistInputBox',
  props: {
    current: {
      type: Object,
      default () {
        return {}
      }
    }
  },
  data () {
    return {
      targetEle: null,
      windowListenerHandler: null
    }
  },
  mounted () {
    this.$bus.on('close-picklist-dropdown', () => {
      this.targetEle = null
    })
    this.$nextTick(function () {
      this.windowListenerHandler = () => {
        if (this.targetEle) {
          let position = this.calculatePosition(this.targetEle, this.targetEle.getBoundingClientRect())
          this.$bus.emit('open-picklist-dropdown', position)
        }
      }
      window.addEventListener('resize', this.windowListenerHandler)
      // 兼容IE跟Chrome的滚动事件
      window.addEventListener('mousewheel', this.windowListenerHandler)
      // 兼容Firefox的滚动事件
      window.addEventListener('DOMMouseScroll', this.windowListenerHandler)
    })
  },
  beforeDestroy () {
    this.$bus.off('close-picklist-dropdown')
    window.removeEventListener('resize', this.windowListenerHandler)
    window.removeEventListener('mousewheel', this.windowListenerHandler)
    window.removeEventListener('DOMMouseScroll', this.windowListenerHandler)
  },
  methods: {
    openDropdown (event) {
      this.targetEle = event.target
      let position = this.calculatePosition(this.targetEle, this.targetEle.getBoundingClientRect())
      this.$bus.emit('open-picklist-dropdown', position)
    },
    calculatePosition (targetEle, targetRect) {
      let position = {
        width: '',
        left: '',
        top: ''
      }
      position.width = `${targetEle.clientWidth}px`
      position.left = `${targetRect.left}px`
      // 32 is the input client height
      position.top = `${targetRect.top + 32}px`
      return position
    }
  }
}
</script>

<style scoped>
  .slds-combobox__input {
    cursor: pointer;
  }
</style>
