<template>
  <div>
    <typeahead
      :is-danger="isDanger"
      :ld-selected-item="selected"
      :ld-metadatas="ldMetadatas"
      :ld-search-items="searchRecentItems"
      :ld-typeahead="search"
      :ld-show-title="ldShowTitle"
      :is-disabled="isDisabled"
      @select="select($event)"
      @remove="remove()"
      @selectObj="selectObj($event)"
      @search="openSearch($event)"
      @opened="lookupListOpened"
      @self-link="goToSelfLink"
      ref="typeahead">
    </typeahead>
    <lookup-advanced-modal
      :ld-show="showAdvancedModal"
      :ld-metadata="ldMetadata"
      :ld-keyword="keyword"
      :ld-search-items="searchRecentItems"
      :ld-search="search"
      :condition="condition"
      :order-by="orderBy"
      @select="select($event)">
    </lookup-advanced-modal>
  </div>
</template>

<script>
import Typeahead from './Typeahead.vue'
import LookupAdvancedModal from './LookupAdvancedModal.vue'
import SearchMixin from './search-mixin.js'

export default {
  mixins: [SearchMixin],
  components: {
    Typeahead,
    LookupAdvancedModal
  },
  data () {
    return {
      selected: null,
      keyword: '',
      showAdvancedModal: -1,
      ldMetadata: {
        label: '',
        labelPural: '',
        icon: '',
        linkField: {
          name: '',
          label: ''
        },
        fields: []
      }
    }
  },
  props: {
    ldSelectedItem: {
      default () {
        return null
      }
    },
    ldMetadatas: {
      default () {
        return null
      }
    },
    ldShowTitle: {
      type: Boolean,
      default () {
        return false
      }
    },
    isDisabled: {
      type: Boolean,
      default () {
        return false
      }
    },
    isDanger: {
      type: Boolean,
      default () {
        return false
      }
    }
  },
  mounted () {
    if (Object.prototype.toString.call(this.ldMetadatas) === '[object Array]') {
      this.ldMetadata = this.ldMetadatas[0]
    } else {
      this.ldMetadata = this.ldMetadatas
    }
    this.selected = this.ldSelectedItem ? Object.assign({}, this.ldSelectedItem) : null
  },
  methods: {
    openSearch (event) {
      this.keyword = event.keyword
      this.showAdvancedModal = new Date().getTime()
    },
    select (event) {
      this.selected = event
      this.$emit('select', event, this.ldMetadata)
    },
    remove () {
      this.$emit('remove', this.ldMetadata)
    },
    clear () {
      this.$refs.typeahead.text = ''
      this.$refs.typeahead.remove()
    },
    selectObj (event) {
      this.ldMetadata = event
      this.$emit('selectObj', event)
    },
    lookupListOpened () {
      this.$emit('opened')
    },
    close () {
      this.$refs.typeahead.close()
    },
    goToSelfLink () {
      this.$emit('self-link')
    }
  },
  watch: {
    ldSelectedItem () {
      this.selected = this.ldSelectedItem ? Object.assign({}, this.ldSelectedItem) : null
    },
    ldMetadatas (newValue) {
      if (Array.isArray(newValue)) {
        this.ldMetadata = newValue[0]
      } else {
        this.ldMetadata = newValue
      }
    }
  }
}
</script>
