import Message from './Message'

class WeChatResponseSettings {
  static from ({ mode, items }) {
    return new WeChatResponseSettings(
      mode,
      items.map((data) => WeChatResponseSettings.createItem(data))
    )
  }

  static createItem (data) {
    return new Message(data)
  }

  constructor (mode, items) {
    this.mode = mode || 'Random'
    this.items = items
  }

  toPascalData () {
    return {
      Mode: this.mode,
      Items: this.items.map((item) => item.toPascalData())
    }
  }
}

export default WeChatResponseSettings
