import ErrorParser from './ErrorParser'

const plugin = {
  install (Vue) {
    if (Vue.prototype.$parseErrorMessage) {
      throw new Error('[Plugin] conflict member `$parseErrorMessage`')
    }

    Vue.prototype.$parseErrorMessage = function (error, reloadWhenTokenExpired = true) {
      const parser = new ErrorParser(error)
      if (parser.isSessionExpired() && reloadWhenTokenExpired) {
        window.location.reload()

        return
      }

      let message = ''
      if (parser.isTimeout() || parser.isOffline()) {
        /*
          todo: move the error message to i18n resource file，
          可能无法工作，这里的 this 指的是具体的某个 Vue Component
        */
        message = 'Check your Internet connection and try again.'
      } else {
        message = parser.parseMessage()
      }

      return message
    }

    if (Vue.prototype.$handleError) {
      throw new Error('[Plugin] conflict member `$handleError`')
    }

    Vue.prototype.$handleError = function (error) {
      const message = this.$parseErrorMessage(error)

      if (!message) {
        console.warn('[Error Handler] got an error, but cannot paser error message', error)

        return
      }

      this.$toast({
        content: message,
        type: 'error'
      })
    }

    if (Vue.prototype.$try) {
      throw new Error('[Plugin] conflict member `$try`')
    }

    Vue.prototype.$try = async function (fn) {
      if (typeof fn !== 'function') {
        throw new Error('fn must be a function')
      }

      try {
        await fn()
      } catch (err) {
        this.$handleError(err)
      }
    }
  }
}

export default plugin
