<template>
  <div class="toggle-section">
    <label class="slds-form-element__label cursor-label" for="" @click="toggle()">{{ labelInfo }}</label>
    <div class="toggle-info">
      <div
        class="response-enabled-button"
        :class="{'response-disabled-btn': !isOn}"
        @click="toggle()"
        @mouseover="over($event)"
        @mouseout="out"
      >
        <span class="slds-icon_container check-icon-span" v-if="isOn">
          <icon name="check" icon-class-name="slds-icon slds-icon_xx-small"></icon>
        </span>
        <div
          :id="animateId"
          :style="{'background-color': disabled ? '#dddbda' : '#fff'}"
        ></div>
      </div>
      <div class="response-enabled">
        {{ isOn ? statuses[0] : statuses[1] }}
      </div>
    </div>
  </div>
</template>

<script>
const uniqueId = () => {
  return Math.random().toString(36).slice(4)
}
export default {
  name: 'Toggle',
  props: {
    labelInfo: {
      type: String,
      default () {
        return ''
      }
    },
    statuses: {
      type: Array,
      default () {
        return ['Enabled', 'Disabled']
      }
    },
    isNewOpened: {
      type: Boolean,
      default () {
        return false
      }
    },
    disabled: {
      type: Boolean,
      default () {
        return false
      }
    }
  },
  created () {
    this.isOn = this.isNewOpened
    this.$nextTick(() => {
      const animateDiv = document.getElementById(this.animateId)
      animateDiv.className = this.isOn ? 'is-toggle' : ''
    })
  },
  data () {
    return {
      isOn: false,
      animateId: uniqueId()
    }
  },
  methods: {
    toggle () {
      if (this.disabled) {
        return
      }
      this.isOn = !this.isOn
      const animateDiv = document.getElementById(this.animateId)
      animateDiv.className = this.isOn ? 'toggle-on' : 'toggle-off'
      this.$emit('toggle', this.isOn)
    },
    over (event) {
      this.$emit('over', event)
    },
    out () {
      this.$emit('out')
    },
    reverse () {
      this.isOn = !this.isOn
      const animateDiv = document.getElementById(this.animateId)
      animateDiv.className = this.isOn ? 'toggle-on' : 'toggle-off'
    }
  }
}
</script>

<style lang="scss" scoped>
  .toggle-section {
    .cursor-label {
      cursor: pointer;
    }
    .toggle-info {
      display: inline-block;
      vertical-align: middle;
      .response-enabled-button {
        margin-top: 8px;
        width: 48px;
        height: 24px;
        background-color: #0070D2;
        border-radius: 12px;
        position: relative;
        cursor: pointer;
        .check-icon-span {
          position: absolute;
          left: 6px;
          top: 4px;
        }
        div {
          width: 20px;
          height: 20px;
          border-radius: 10px;
          background: #fff;
          position: absolute;
          top: 2px;
          left: 2px;
          cursor: pointer;
        }
        .is-toggle {
          left: 26px;
        }
      }
      .response-enabled {
        font-size: 10px;
        color: #706E6B;
      }
      .response-disabled-btn {
        background-color: #7e8c99;
      }
      .toggle-on {
        animation: bounce-in .2s linear;
        animation-fill-mode: forwards;
      }
      @keyframes bounce-in {
        0% {
          left: 2px;
        }
        50% {
          left: 14px;
        }
        100% {
          left: 26px;
        }
      }

      .toggle-off {
        animation: bounce-out .2s linear;
        animation-fill-mode: forwards;
      }
      @keyframes bounce-out {
        0% {
          left: 26px;
        }
        50% {
          left: 14px;
        }
        100% {
          left: 2px;
        }
      }
    }
  }
</style>
