import {
  SET_SMS_INTEGRATION_SETTINGS,
  SET_SMS_ENABLED
} from '../mutation-types'
import { CharketSmsSettingsService } from '../../service'

// initial state
const state = {
  smsIntegrationSettings: undefined
}

// mutations
const mutations = {
  [SET_SMS_INTEGRATION_SETTINGS] (state, settings) {
    state.smsIntegrationSettings = settings
  },
  [SET_SMS_ENABLED] (state, enabled) {
    state.smsIntegrationSettings.IsCharketSmsEnabled = enabled
  }
}

const actions = {
  async getSMSIntegrationSettings ({ commit }) {
    try {
      const settings = await CharketSmsSettingsService.getCharketSmsSettings()
      commit(SET_SMS_INTEGRATION_SETTINGS, settings)
    } catch (error) {
      throw error
    }
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}
