<template>
  <div id="charket-home" class="home slds-brand-band_cover slds-brand-band_medium slds-template_default" :class="{'slds-brand-band': !isScrollContainer, 'slds-scrollable_y': isScrollContainer}">
    <div class="slds-page-header nav-header">
      <div class="slds-page-header__row">
        <div class="slds-page-header__col-title">
          <div class="slds-media">
            <div class="slds-media__figure">
              <slot name="header-icon">
                <span class="slds-icon_container header-icon">
                  <icon name="settings" icon-class-name="slds-icon slds-icon--small">
                  </icon>
                </span>
              </slot>
            </div>
            <slot name="header-title">
              <div class="slds-media__body">
                <p class="slds-page-header__name-meta">
                  <a class="slds-text-body_regular" href="javascript:void(0);" tabindex="-1" @click="goToDefaultPage">{{ header.label }}</a>
                </p>
                <div class="slds-page-header__name">
                  <div class="slds-page-header__name-title">
                    <h1>
                      <span class="slds-page-header__title slds-truncate" :title="header.name">{{ header.name }}</span>
                    </h1>
                  </div>
                </div>
              </div>
            </slot>
          </div>
        </div>
      </div>
      <div class="slds-page-header__col-actions">
        <div class="slds-page-header__controls">
          <div class="slds-page-header__control">
            <ul class="slds-button-group-list">
              <slot name="button-group">
              </slot>
            </ul>
          </div>
        </div>
      </div>
    </div>
    <div class="slds-page-header content" :class="{'hide-padding': hideContentPadding, 'content-has-list': isListContent, 'slds-scrollable_y': !isListContent && !secondContent}" v-if="!isLoading && !hideHeader">
      <slot name="content">
      </slot>
    </div>
    <slot name="second-content" v-if="secondContent && !isLoading">
    </slot>
    <slot name="third-content" v-if="!isLoading">
    </slot>
    <loading v-if="isLoading">
    </loading>
  </div>
</template>

<script>

export default {
  name: 'RightPage',
  props: {
    header: {
      type: Object,
      default () {
        return { label: 'CHARKET SETTINGS', name: 'Home' }
      }
    },
    hideHeader: {
      type: Boolean,
      default () {
        return false
      }
    },
    modalButtonName: {
      type: String,
      default () {
        return 'Save'
      }
    },
    secondContent: {
      type: Boolean,
      default () {
        return false
      }
    },
    hideContentPadding: {
      type: Boolean,
      default () {
        return false
      }
    },
    isListContent: {
      type: Boolean,
      default () {
        return false
      }
    },
    isScrollContainer: {
      type: Boolean,
      default () {
        return false
      }
    }
  },
  data () {
    return {
      isLoading: true
    }
  },
  mounted () {
    this.$bus.on('show-loading', () => {
      this.isLoading = true
    })
    this.$bus.on('hide-loading', () => {
      this.isLoading = false
    })
  },
  beforeDestroy () {
    this.$bus.off('show-loading')
    this.$bus.off('hide-loading')
  },
  methods: {
    goToDefaultPage () {
      const sidebarEle = document.getElementById('charket-sidebar')
      sidebarEle.scrollTop = 0
      this.$router.push({ path: '/' })
    }
  }
}
</script>

