<template>
  <transition name="fade" v-if="show">
    <div id="charket-settings-toast" class="slds-notify_container slds-is-absolute">
      <div class="slds-notify slds-notify_toast"
           :class="[`slds-theme_${type}`]"
           role="status" @mouseover="onMouseover()" @mouseout="onMouseout()">
        <span class="slds-assistive-text">{{ type }}</span>
        <span class="slds-icon_container slds-m-right_small slds-no-flex slds-align-top"
              :class="[`slds-icon-utility-${type}`]">
          <icon icon-class-name="slds-icon slds-icon_small" :name="type">
          </icon>
        </span>
        <div class="slds-notify__content">
          <slot>
          </slot>
        </div>
        <div class="slds-notify__close">
          <button class="slds-button slds-button_icon slds-button_icon-inverse" @click="onClickClose()">
            <icon class="slds-button__icon slds-button__icon_large" name="close">
            </icon>
            <span class="slds-assistive-text">Close</span>
          </button>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>

export default {
  name: 'Toast',
  props: {
    type: {
      type: String,
      default () {
        return 'success'
      }
    },
    autoHide: {
      type: Boolean,
      default () {
        return false
      }
    }
  },
  data () {
    return {
      timeoutHandle: null,
      show: false
    }
  },
  methods: {
    open () {
      if (this.timeoutHandle) {
        window.clearTimeout(this.timeoutHandle)
        this.timeoutHandle = null
      }

      if (this.autoHide) {
        this.timeoutHandle = setTimeout(() => {
          this.show = false
          this.$emit('close', { source: 'automaticall' })
        }, 3000)
      }

      this.show = true
    },
    hide () {
      this.show = false
      this.$emit('close', { source: 'programmatically' })
    },
    onClickClose () {
      this.show = false
      this.$emit('close', { source: 'manually' })
    },
    onMouseover () {
      if (!this.timeoutHandle) {
        return
      }

      window.clearTimeout(this.timeoutHandle)
      this.timeoutHandle = null
    },
    onMouseout () {
      if (!this.autoHide) {
        return
      }

      this.timeoutHandle = setTimeout(() => {
        this.show = false
        this.$emit('close', { source: true })
      }, 3000)
    }
  }
}
</script>

<style lang="scss" scoped>
  .fade-enter-active, .fade-leave-active {
    transition: opacity 1s;
  }
  .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
    opacity: 0;
  }
  #charket-settings-toast {
    left: calc(50% - 300px);
    width: 600px;
  }
</style>
