<template>
  <ld-modal
    class="confirm-modal"
    :title="title"
    ref="ConfirmModal"
  >
    <div slot="content">
      <div class="confirm-modal__content slds-p-around_medium">
        {{ content }}
      </div>
    </div>

    <div slot="footer">
      <ld-button
        neutral
        :disabled="isConfirming"
        @click="onClickCancel()"
      >
        {{ cancel }}
      </ld-button>
      <ld-button
        brand
        :disabled="isConfirming"
        @click="onClickConfirm()"
      >
        {{ ok }}
      </ld-button>
    </div>
  </ld-modal>
</template>

<script>
import { LdModal } from 'charket-ui/dist/ld-modal.js'
import { LdButton } from 'charket-ui/dist/ld-button.js'
/**
 * @example
  // this.$confirm is provide by `src/mixins/global.js`
  const confirm = this.$confirm({
    title: 'foo',
    content: 'bar',
    ok: 'OK', // optional
    cancel: 'Cancel', // optional
  })

  confirm.$on('ok', () => {
    try {
      // your actions
      await doSomething()
      confirm.close()
    } catch (error) {
      // handle error
      confirm.resume()
      // or
      confirm.close()
    }
  })

  confirm.$on('cancel', () => {
    // clean?
  })
  */
export default {
  components: {
    LdModal,
    LdButton
  },
  data () {
    return {
      defaultOK: 'OK',
      defaultCancel: 'Cancel',
      title: '',
      content: '',
      ok: '',
      cancel: '',
      isConfirming: false
    }
  },
  methods: {
    open (options) {
      const { title, content, ok, cancel } = options
      this.title = title
      this.content = content
      this.ok = ok || this.defaultOK
      this.cancel = cancel || this.defaultCancel

      this.$refs.ConfirmModal.open()

      return this
    },
    resume () {
      this.isConfirming = false
    },
    close () {
      this.$refs.ConfirmModal.close()
      this.$off('ok')
      this.$off('cancel')
      this.isConfirming = false
    },
    onClickCancel () {
      this.$emit('cancel')
      this.close()
    },
    onClickConfirm () {
      this.isConfirming = true
      this.$emit('ok')
    }
  }
}
</script>

<style lang="scss" scoped>
  .confirm-modal {
    &__content {
      text-align: center;
    }
  }
</style>
