<template>
  <right-page :header="rightPageHeader" :hide-content-padding="true" :is-list-content="true">
    <div slot="content" class="message-hanlers-list-page">
      <div class="message-handlers-actions">
        <ld-button
          neutral
          :disabled="!hasChanged || isSaving"
          @click="cancel"
        >
          Cancel
        </ld-button>
        <ld-button
          brand
          :disabled="!hasChanged || isSaving"
          @click="save"
        >
          Save
        </ld-button>
      </div>
      <ld-tabs
        v-if="showTabs"
        class="message-handlers-tabs"
        :selected-index="selectedTabIndex"
        @changed="onChangeTab($event)"
      >
        <ld-tab
          class="message-handlers-tab"
          label="Before Handlers"
        >
          <list-view
            v-if="selectedTabIndex === 0"
            :type="handlersType"
            execution-order="Before"
            @changed="onChanged"
          ></list-view>
        </ld-tab>

        <ld-tab
          class="message-handlers-tab"
          label="After Handlers"
        >
          <list-view
            v-if="selectedTabIndex === 1"
            :type="handlersType"
            execution-order="After"
            @changed="onChanged"
          ></list-view>
        </ld-tab>
      </ld-tabs>
    </div>
  </right-page>
</template>

<script>
import ListView from './ListView.vue'
import { LdButton } from 'charket-ui/dist/ld-button.js'
import { LdTabs, LdTab } from 'charket-ui/dist/ld-tabs.js'
import { CharketSettingsService } from '@/service'

export default {
  name: 'WeChatMessageHandlers',
  components: {
    ListView,
    LdButton,
    LdTabs,
    LdTab
  },
  data () {
    return {
      selectedTabIndex: -1,
      isSaving: false,
      hasChanged: false,
      handlers: null,
      handlersType: 'Message',
      handlersExecutionOrder: 'Before',
      showTabs: true
    }
  },
  computed: {
    rightPageHeader () {
      return {
        label: 'CHARKET SETTINGS',
        name: `WeChat ${this.handlersType} Handlers`
      }
    }
  },
  watch: {
    '$route' (newVal, oldVal) {
      const isSwitchFromEventToMessage = newVal.name.includes('WeChatMessageHandlers') && oldVal.name.includes('WeChatEventHandlers')
      const isSwitchFromMessageToEvent = newVal.name.includes('WeChatEventHandlers') && (oldVal.name.includes('WeChatMessageHandlers'))
      const isSwitchFromMessageAfterToMessage = newVal.name.includes('WeChatMessageHandlersBefore') && (oldVal.name.includes('WeChatMessageHandlers'))
      const isSwitchFromEventAfterToEvent = newVal.name.includes('WeChatEventHandlersBefore') && (oldVal.name.includes('WeChatEventHandlers'))
      if (
        isSwitchFromEventToMessage ||
        isSwitchFromMessageToEvent ||
        isSwitchFromMessageAfterToMessage ||
        isSwitchFromEventAfterToEvent
      ) {
        this.selectedTabIndex = 0
        this.hasChanged = false
        this.rerenderTabs()
      }
      if (this.$route.path.includes('custom-handler')) {
        this.initHandlerType()
        this.changeTabIndex()
      }
    }
  },
  mounted () {
    this.$bus.emit('hide-loading')
    this.initHandlerType()
    this.selectedTabIndex = this.getTabIndex()
  },
  methods: {
    initHandlerType () {
      if (this.$route.path.includes('message-handlers')) {
        this.handlersType = 'Message'
      } else if (this.$route.path.includes('event-handlers')) {
        this.handlersType = 'Event'
      }
      if (this.$route.path.includes('handlers/before')) {
        this.handlersExecutionOrder = 'Before'
      } else if (this.$route.path.includes('handlers/after')) {
        this.handlersExecutionOrder = 'After'
      }
    },
    changeTabIndex () {
      this.selectedTabIndex = -1
      this.$nextTick(() => {
        this.selectedTabIndex = this.getTabIndex()
      })
    },
    getTabIndex () {
      if (this.$route.path.includes('handlers/before')) {
        return 0
      } else if (this.$route.path.includes('handlers/after')) {
        return 1
      }
    },
    onChangeTab (tab) {
      this.hasChanged = false
      this.selectedTabIndex = -1
      this.$nextTick(() => {
        const typeRouter = {
          Message: 'message',
          Event: 'event'
        }
        if (tab.index === 0) {
          this.$router.replace({ path: `/custom-handler/${typeRouter[this.handlersType]}-handlers/before` })
        } else if (tab.index === 1) {
          this.$router.replace({ path: `/custom-handler/${typeRouter[this.handlersType]}-handlers/after` })
        }
        this.selectedTabIndex = tab.index
      })
    },
    cancel () {
      this.hasChanged = false
      this.changeTabIndex()
    },
    async save () {
      const invalidHandler = this.handlers.find(h => !h.ApexClassId)
      if (invalidHandler && this.handlers.length === 1) {
        this.handlers = []
      } else if (invalidHandler) {
        this.$bus.emit('error', { content: 'These required fields must be completed: Apex Class.', type: 'error' })
        return
      }
      this.isSaving = true
      await this.$try(async () => {
        await CharketSettingsService.saveCustomHandlers(this.handlersType, this.handlersExecutionOrder, this.handlers)
        this.$bus.emit('success', { content: 'Your changes are saved.', type: 'success' })
        this.hasChanged = false
        this.changeTabIndex()
      })
      this.isSaving = false
    },
    onChanged (handlers) {
      this.hasChanged = true
      this.handlers = handlers
    },
    rerenderTabs () {
      this.showTabs = false
      this.$nextTick(() => {
        this.showTabs = true
      })
    }
  }
}
</script>

<style lang="scss" scoped>
  .message-hanlers-list-page {
    height: 100%;
    position: relative;
    .message-handlers-tabs {
      height: 100%;
      .message-handlers-tab {
        height: calc(100% - 40px);
      }
    }
    .message-handlers-actions {
      position: absolute;
      right: 16px;
      top: 5px;
      z-index: 1;
    }
  }
</style>
