import { CharketSmsSettingsTransport } from '../transport'

const transport = new CharketSmsSettingsTransport()

class CharketSmsSettingsService {
  static getCharketSmsSettings () {
    return transport.getCharketSmsSettings()
  }

  static saveCharketSmsSettings ({ IsCharketSmsEnabled = false }) {
    return transport.saveCharketSmsSettings({ IsCharketSmsEnabled })
  }

  static getVerificationCodeSettings () {
    return transport.getVerificationCodeSettings()
  }

  static saveVerificationCodeSettings (settingsJSON) {
    return transport.saveVerificationCodeSettings(settingsJSON)
  }
}

export default CharketSmsSettingsService
