const specialKeyMap = new Map()
const getCamelCaseKey = (key) => {
  const words = key.split('', key.length)
  const firstLowerCaseIndex = words.findIndex(word => !/[A-Z]/.test(word))

  if (firstLowerCaseIndex === -1) {
    const stylizedKey = key.toLowerCase()
    specialKeyMap.set(stylizedKey, key)
    return stylizedKey
  } else if (firstLowerCaseIndex === 0) {
    return key
  } else if (firstLowerCaseIndex === 1) {
    return key.substring(0, 1).toLowerCase() + key.substring(1)
  } else {
    const stylizedKey = key.substring(0, firstLowerCaseIndex - 1).toLowerCase() + key.substring(firstLowerCaseIndex - 1)
    specialKeyMap.set(stylizedKey, key)
    return stylizedKey
  }
}

const getPascalCaseKey = (key) => {
  return specialKeyMap.get(key) || key.substring(0, 1).toUpperCase() + key.substring(1)
}
const getStylizedKey = (key, style = '') => {
  if (!key) {
    return key
  }

  if (style.toLowerCase() === 'camel') {
    return getCamelCaseKey(key)
  } else if (style.toLowerCase() === 'pascal') {
    return getPascalCaseKey(key)
  } else {
    throw new Error(`Invalid style. (${style})`)
  }
}

const stylized = (data, style) => {
  if (!data) {
    return data
  }

  const result = {}
  const keys = Object.keys(data)
  keys.forEach((key) => {
    const value = data[key]
    const stylizedKey = getStylizedKey(key, style)
    if (toString.apply(value) === '[object Array]') {
      result[stylizedKey] = value.map((i) => stylized(i, style))
    } else if (toString.apply(value) === '[object Object]') {
      result[stylizedKey] = stylized(value, style)
    } else {
      result[stylizedKey] = value
    }
  })

  return result
}

const camelize = (data) => {
  return stylized(data, 'Camel')
}

const pascalize = (data) => {
  return stylized(data, 'Pascal')
}

class Model {
  constructor (data) {
    Object.assign(this, camelize(data))
  }

  toPascalData () {
    return pascalize(this)
  }

  getData () {
    return JSON.parse(JSON.stringify(this))
  }
}

export default Model
