import { MessageService } from '../../../service'

const DEFAULT_PAGE_SIZE = 20

class TextMessageDataProvider {
  constructor (options) {
    this.options = options
  }

  async get ({ keyword = '', limit = DEFAULT_PAGE_SIZE, offset = 0 }) {
    const messages = await MessageService.getMessages({
      type: 'Text Message',
      keyword,
      limit,
      offset,
      wechatAccountId: this.options.wechatAccount.id
    })

    return messages
  }

  create (content) {
    return MessageService.createTextMessage({
      wechatAccountId: this.options.wechatAccount.id,
      content
    })
  }

  update (id, content) {
    return MessageService.updateTextMessage({ id, content })
  }
}

export default TextMessageDataProvider
