import Transport from './Transport'

const ACTION_BASE_NAME = 'SObjectRemoteActions'

class SObjectTransport extends Transport {
  constructor () {
    super(ACTION_BASE_NAME)
  }

  getAllMergeableSObjects (searchKey) {
    return this.invoke('getAllMergeableSObjects', [searchKey], { keepNamespace: true })
  }

  getObjectsByPageName (pageName) {
    return this.invoke('getMergeableSObjects', [pageName], { keepNamespace: true })
  }

  getFields (objectName) {
    return this.invoke('getFields', [objectName], { keepNamespace: true })
  }
}

export default SObjectTransport
