<template>
  <div>
    <input-box
      :is-danger="isDanger"
      :current="currentItem"
      @remove="removeCurrent" refs="lookupInput">
    </input-box>
    <dropdown @selected="selectWeChatAccount">
    </dropdown>
    <advanced-modal :is-show="showAdvancedModal" @selected="selectWeChatAccount">
    </advanced-modal>
  </div>
</template>

<script>
import InputBox from './InputBox'
import Dropdown from './Dropdown'
import AdvancedModal from './AdvancedModal'

export default {
  name: 'Lookup',
  components: {
    InputBox,
    Dropdown,
    AdvancedModal
  },
  props: {
    current: {
      type: Object,
      default () {
        return {}
      }
    },
    isDanger: {
      type: Boolean,
      default () {
        return false
      }
    }
  },
  data () {
    return {
      currentItem: {},
      showAdvancedModal: 0
    }
  },
  watch: {
    'current.id' () {
      this.currentItem = this.current
    }
  },
  created () {
    this.currentItem = this.current
  },
  methods: {
    removeCurrent () {
      this.currentItem = {}
      this.$emit('remove')
    },
    selectWeChatAccount (account) {
      this.currentItem = { name: account.Name, id: account.Id, type: account.Type__c }
      this.showAdvancedModal = new Date().getTime()
      this.$emit('selected', account)
    }
  }
}
</script>

