<template>
  <div class="charket-typeahead">
    <div class="slds-form-element slds-lookup" :class="{ 'slds-is-open': showLookupList}" data-select="single">
      <label class="slds-form-element__label" :for="inputId" v-if="ldShowTitle || !ldShowDropdwon">
        <span>{{ ldMetadata.linkField.label }}</span>
      </label>
      <div class="slds-form-element__control slds-grid" :class="{'slds-box_border': ldShowDropdwon && !current, 'select-warn-border': !showLookupList && !current && ldShowDropdwon && text}">
        <dropdown :objects="ldMetadatas" :current="ldMetadata" @select="setObject($event)" v-show="!current && ldShowDropdwon && ldMetadatas.length > 1"></dropdown>
        <div v-show="!current || !ldShowDropdwon" class="slds-input-has-icon slds-input-has-icon_right slds-grow">
          <input
            :id="inputId"
            type="search"
            class="slds-lookup__search-input slds-input search-input"
            :class="{'slds-input_bare': ldShowDropdwon, 'is-danger': isDanger}"
            :placeholder="ldMetadata.labelPural ? `Search ${ldMetadata.labelPural}...` : ''"
            role="combobox"
            aria-activedescendent=""
            aria-expanded="true"
            aria-autocomplete="list"
            autocomplete="disabled"
            @keydown.esc="esc($event)"
            @keydown.enter="enter($event)"
            @keyup="up($event)"
            @focus="focus()"
            v-model="text" style="min-height: 30px;"
          />
          <span class="slds-icon_container slds-icon-utility-search slds-input__icon slds-input__icon_right">
            <icon
              icon-class-name="slds-icon slds-icon_x-small slds-icon-text-default"
              name="search"
            ></icon>
          </span>
        </div>
        <div v-if="current && ldShowDropdwon" class="slds-pill_container selected-item slds-size_1-of-1" :class="{'selected-item-disabeld': isDisabled}">
          <span class="slds-pill slds-size_1-of-1 selected-item-pill" :class="{'pill-disabled': isDisabled}">
            <span :class="objectIcon.className" class="slds-icon_container slds-pill__icon_container" v-if="!['caseIcon', 'apex'].includes(objectIcon.name)">
              <icon class="custom-icon" :name="objectIcon.name"></icon>
            </span>
            <span class="slds-icon_container slds-pill__icon_container slds-icon-standard-case" v-if="objectIcon.name === 'caseIcon'">
              <icon :name="'caseIcon'"></icon>
            </span>
            <span class="slds-icon slds-icon_small slds-media__figure slds-icon-standard-apex" v-if="objectIcon.name === 'apex'">
              <svg aria-hidden="true" style="width: 24px; height: 24px; padding: 4px;">
                <use xmlns:xlink="http://www.w3.org/1999/xlink" :xlink:href="'#apex'">
                </use>
              </svg>
            </span>
            <span class="slds-pill__label" :title="current.name || current.Name ">{{current.name || current.Name }}</span>
            <button class="slds-button slds-button_icon slds-pill_remove" style="margin-left: auto" title="Remove" @click="remove()">
              <svg aria-hidden="true" class="slds-button__icon">
                <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#close">
                </use>
              </svg>
              <span class="slds-assistive-text">Remove</span>
            </button>
          </span>
        </div>
      </div>
      <div class="select-warn-notification" v-if="!showLookupList && !current && ldShowDropdwon && text">An invalid option has been chosen.</div>
      <div
        class="slds-lookup__menu"
        :class="{'charket-lookup-menu': showLoading}"
      >
        <ul class="slds-lookup__list" role="listbox">
          <li
            v-if="textSize >= 2"
            @click="clickSeach()"
            role="presentation"
          >
            <span class="slds-lookup__item-action slds-lookup__item-action_label" role="option">
              <icon name="search" width="16px" height="16px" class="slds-icon slds-icon_x-small slds-icon-text-default" charket-icon></icon>
              <span class="slds-truncate"><span v-show="text">{{`${text} in ${ldMetadata.labelPural}`}}</span></span>
            </span>
          </li>
          <li v-if="showLoading" role="presentation">
            <div class="slds-spinner_container">
              <div class="slds-spinner_brand slds-spinner slds-spinner_small" role="alert">
                <span class="slds-assistive-text">Loading</span>
                <div class="slds-spinner__dot-a"></div>
                <div class="slds-spinner__dot-b"></div>
              </div>
            </div>
          </li>
          <li
            v-for="(item, index) in items"
            :key="index"
            @click="select(item)"
            role="presentation"
            v-show="items.length > 0 || textSize >= 2"
          >
            <span class="slds-lookup__item-action slds-media" role="option">
              <span :class="objectIcon.className" class="slds-icon slds-icon_small slds-media__figure" v-if="!['caseIcon', 'apex'].includes(objectIcon.name)">
               <svg aria-hidden="true" style="width: 24px; height: 24px;">
                  <use xmlns:xlink="http://www.w3.org/1999/xlink" :xlink:href="'#' + objectIcon.name">
                  </use>
                </svg>
              </span>
              <span class="slds-icon slds-icon_small slds-media__figure slds-icon-standard-case" v-if="objectIcon.name === 'caseIcon'">
               <svg aria-hidden="true" style="width: 24px; height: 24px;">
                  <use xmlns:xlink="http://www.w3.org/1999/xlink" :xlink:href="'#case'">
                  </use>
                </svg>
              </span>
              <span class="slds-icon slds-icon_small slds-media__figure slds-icon-standard-apex" v-if="objectIcon.name === 'apex'">
               <svg aria-hidden="true" style="width: 24px; height: 24px; padding: 4px;">
                  <use xmlns:xlink="http://www.w3.org/1999/xlink" :xlink:href="'#apex'">
                  </use>
                </svg>
              </span>
              <div class="slds-media__body">
                <div class="slds-lookup__result-text" :title="item.name" :value="item.id" v-html="item.rawHtml || item.name">
                </div>
                <span class="slds-lookup__result-meta slds-text-body_small"><span>{{ldMetadata.label | capitalize}}</span></span>
              </div>
            </span>
          </li>
          <li role="presentation" @click="goToLink()" v-if="ldMetadata.newObjectLink">
            <span class="slds-lookup__item-action slds-lookup__item-action_label" role="option">
                <icon name="add" width="16px" height="16px" class="slds-icon slds-icon_x-small slds-icon-text-default" charket-icon></icon>
              <span class="slds-truncate">New {{ ldMetadata.label | capitalize }}</span>
            </span>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import { capitalize, lowercase } from '../../filters/text.js'
import Dropdown from './Dropdown.vue'
import { getSizeOfUTF8Text } from 'charket-ui/dist/utils/rich-text'

const uniqueId = () => {
  const id = Math.random().toString(36).slice(4)
  return id
}

export default {
  components: {
    Dropdown
  },
  filters: {
    capitalize,
    lowercase
  },
  props: {
    ldSelectedItem: {
      default () {
        return null
      }
    },
    ldMetadatas: {
      default () {
        return null
      }
    },
    ldSearchItems: {
      type: Function,
      default () {
        return function () {}
      }
    },
    ldKeyword: {
      type: String,
      default () {
        return ''
      }
    },
    ldTypeahead: {
      type: Function,
      default () {
        return function () {}
      }
    },
    ldShowDropdwon: {
      type: Boolean,
      default () {
        return true
      }
    },
    ldShowTitle: {
      type: Boolean,
      default () {
        return false
      }
    },
    isDisabled: {
      type: Boolean,
      default () {
        return false
      }
    },
    isDanger: {
      type: Boolean,
      default () {
        return false
      }
    }
  },
  data () {
    return {
      inputId: uniqueId(),
      current: null,
      text: '',
      items: [],
      showLookupList: false,
      typeaheadHandle: null,
      ldMetadata: {
        label: '',
        labelPural: '',
        icon: '',
        linkField: {
          name: '',
          label: ''
        },
        fields: []
      },
      ldItems: [],
      showLoading: false
    }
  },
  computed: {
    objectIcon () {
      let name = ''
      let category = ''
      if (this.ldMetadata.icon) {
        const names = this.ldMetadata.icon.split('.')
        if (names.length === 1) {
          category = 'standard'
          name = names[0]
        } else {
          category = names[0]
          name = names[1]
        }
      }
      let className = 'slds-icon-' + category + '-' + name
      if (category === 'custom') {
        className = `slds-icon-custom slds-icon-standard-custom ${name}-custom`
      }
      return {
        name,
        category,
        className
      }
    },
    textSize () {
      return getSizeOfUTF8Text(this.text.trim())
    }
  },
  watch: {
    ldKeyword (value) {
      this.text = value
    },
    ldSelectedItem (value) {
      this.text = ''
      this.current = value
    },
    text (value) {
      if (this.typeaheadHandle) {
        clearTimeout(this.typeaheadHandle)
      }
      if (this.textSize >= 2 && this.showLookupList) {
        this.typeaheadHandle = setTimeout(() => {
          this.typeahead()
        }, 600)
      }
    },
    ldMetadatas (value) {
      if (Object.prototype.toString.call(this.ldMetadatas) === '[object Array]') {
        this.ldMetadata = this.ldMetadatas[0]
      } else {
        this.ldMetadata = this.ldMetadatas
      }
    }
  },
  mounted () {
    this.current = this.ldSelectedItem
    // TODO: compatibility
    this.searchBox = this.$el.getElementsByTagName('input')[0]
    document.addEventListener('click', (event) => {
      setTimeout(() => {
        if (event.target !== this.searchBox) {
          this.showLookupList = false
        }
      }, 100)
    })
    if (Object.prototype.toString.call(this.ldMetadatas) === '[object Array]') {
      this.ldMetadata = this.ldMetadatas[0]
    } else {
      this.ldMetadata = this.ldMetadatas
    }
  },
  methods: {
    clickSeach () {
      const event = {
        keyword: this.text,
        metadata: this.ldMetadata
      }
      this.$emit('search', event)
    },
    async typeahead () {
      this.showLoading = true
      try {
        const items = await this.ldTypeahead(this.text.trim(), this.ldMetadata)
        this.filterItems(items || [])
      } catch (error) {
        this.$handleError(error)
      } finally {
        this.showLoading = false
      }
    },
    filterItems (items) {
      const arr = [...items]
      this.items.splice(0, this.items.length)
      const keyword = this.text ? this.text.toLowerCase().trim() : ''
      arr.forEach((item) => {
        const id = item.id
        const name = item.name
        const matchIndex = name.toLowerCase().indexOf(keyword)
        if (matchIndex > -1) {
          const rawHtml = [
            name.substring(0, matchIndex),
            '<mark>',
            name.substring(matchIndex, matchIndex + keyword.length),
            '</mark>',
            name.substring(matchIndex + keyword.length)
          ].join('')

          this.items.push({
            id,
            name,
            rawHtml
          })
        }
      })
    },
    up (event) {
      if (event.code === 'Escape' || event.code === 'Enter') {
        // nothing
      } else if (this.textSize < 2) {
        this.filterItems(this.ldItems)
        this.showLookupList = true
      } else if (this.textSize >= 2) {
        this.filterItems(this.items)
        this.showLookupList = true
      } else {
        this.showLookupList = true
      }
    },
    enter (event) {
      if (this.textSize >= 2) {
        event.target.blur()
        setTimeout(() => {
          this.showLookupList = false
        }, 100)
        this.clickSeach()
      }
    },
    esc (event) {
      event.preventDefault()
      this.showLookupList = false
    },
    focus () {
      setTimeout(async () => {
        this.items = []
        this.showLookupList = true
        this.showLoading = true
        this.$emit('opened')
        try {
          this.ldItems = await this.ldSearchItems(this.ldMetadata)
          this.filterItems(this.ldItems || [])
        } catch (error) {
          this.$handleError(error)
        } finally {
          this.showLoading = false
        }
      }, 100)
    },
    close () {
      this.showLookupList = false
    },
    remove () {
      this.current = null
      this.filterItems(this.ldItems)
      this.$emit('remove')
    },
    select (item) {
      this.current = item
      this.showLookupList = false
      delete item.rawHtml
      this.$emit('select', item)
    },
    goToLink () {
      if (this.ldMetadata.newObjectLink === 'Self Link') {
        this.$emit('self-link')
      } else {
        window.open(this.ldMetadata.newObjectLink)
      }
    },
    setObject (event) {
      this.ldMetadata = event
      this.$emit('selectObj', this.ldMetadata)
    }
  }
}
</script>
<style>
  .charket .slds-lookup .slds-icon-custom:not(.slds-pill__icon_container) svg {
    padding: 5px;
  }
  .charket .slds-lookup .slds-icon-custom.slds-pill__icon_container svg {
    padding: 3px;
  }
</style>
