        background-color: #dddbda;
        pointer-events: none;
      }
    }
    .selected-item-disabeld {
      background-color: #dddbda;
      border-color: #a8b7c7;
      cursor: not-allowed;
    }
    .select-warn-border {
      border: 1px solid rgb(194, 57, 52);
    }
    .select-warn-notification {
      font-size: .75rem;
      color: rgb(194, 57, 52);
    }
    .charket-lookup-menu {
      min-height: 37px;
    }
    .qrcode-custom {
      background-color: #575ACA;
    }
    .forms-custom {
      background-color: #973CB7;
    }
    .survey-custom {
      background-color: rgb(115, 147, 228);
    }
    .is-danger {
      border-color: rgb(194, 57, 52);
      box-shadow: rgb(194, 57, 52) 0 0 0 1px inset;
      background-clip: padding-box;
    }

    .custom-icon {
      width: 20px;
      height: 20px;
    }
  }
</style>
