<template>
  <div class="slds-dropdown slds-dropdown_length-with-icon-7 slds-dropdown_fluid"
       role="listbox"
       :style="{position: 'fixed', transform: 'none', left: position.left, top: position.top, width: position.width, zIndex: 10000}"
       :class="{'charket-lookup-dropdown': showLoading}"
       @click.stop=""
       v-if="show">
    <ul class="slds-listbox slds-listbox_vertical" role="presentation">
      <li role="presentation" class="slds-listbox__item" v-if="textSize >= 2" @click="openDetailModal">
        <div aria-selected="true" class="slds-media slds-listbox__option slds-listbox__option_entity slds-listbox__option_term slds-has-focus" role="option">
          <span class="slds-media__figure slds-listbox__option-icon">
            <span class="slds-icon_container slds-icon-utility-search" title="Search for term: ">
              <icon name="search" icon-class-name="slds-icon slds-icon_x-small slds-icon-text-default">
              </icon>
              <span class="slds-assistive-text">Search for term: </span>
            </span>
          </span>
          <span class="slds-media__body">
            <span class="slds-listbox__option-text slds-listbox__option-text_entity">{{ searchLabel }}</span>
          </span>
        </div>
      </li>
      <li v-if="showLoading">
        <loading size="small">
        </loading>
      </li>
      <li role="presentation" class="slds-listbox__item" v-for="(account, index) in wechatAccounts" :key="index" @click="selectWeChatAccount(account)">
        <div class="slds-media slds-listbox__option slds-listbox__option_entity slds-listbox__option_has-meta" role="option">
          <span class="slds-media__figure slds-listbox__option-icon">
            <span class="slds-icon_container slds-icon-standard-wechat-account">
              <icon
                name="wechat_account_miniprogram"
                :icon-style="iconStyle"
                icon-class-name="slds-icon slds-icon_small"
                v-if="account.Type__c === 'Mini Program'"
              >
              </icon>
              <icon
                name="wechat_account_service"
                :icon-style="iconStyle"
                icon-class-name="slds-icon slds-icon_small"
                v-if="account.Type__c === 'Service Account'"
              >
              </icon>
              <icon
                name="wechat_account_subscription"
                :icon-style="iconStyle"
                icon-class-name="slds-icon slds-icon_small"
                v-if="account.Type__c === 'Subscription Account'"
              >
              </icon>
            </span>
          </span>
          <span class="slds-media__body">
            <span class="slds-listbox__option-text slds-listbox__option-text_entity" v-html="account.rawHtml">
            </span>
            <span class="slds-listbox__option-meta slds-listbox__option-meta_entity">WeChat Account</span>
          </span>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
import LookupMixin from './LookupMixin'
import { getSizeOfUTF8Text } from 'charket-ui/dist/utils/rich-text'

export default {
  name: 'LookupDropdown',
  mixins: [LookupMixin],
  data () {
    return {
      keyword: '',
      position: {},
      showLoading: false,
      show: false,
      hideListener: null,
      allAccounts: [],
      iconStyle: { width: '24px', height: '24px', padding: '4px' }
    }
  },
  computed: {
    searchLabel () {
      return this.keyword ? `${this.keyword} in WeChat Accounts` : 'WeChat Accounts'
    },
    textSize () {
      return getSizeOfUTF8Text(this.keyword.trim())
    }
  },
  created () {
    this.hideListener = () => {
      this.show = false
      this.$bus.emit('close-dropdown', null)
    }
    window.addEventListener('click', this.hideListener)
  },
  mounted () {
    this.$bus.on('open-dropdown', async (valueObj) => {
      this.show = true
      this.keyword = valueObj.keyword ? valueObj.keyword.trim() : ''
      this.position = valueObj.position
      let queryType = valueObj.type
      if (queryType === 'orientation') {
        return null
      }
      if (queryType === 'search') {
        this.showLoading = true
        await this.fetchWeChatAccounts(this.keyword)
        if (!this.keyword) {
          this.allAccounts = [...this.wechatAccounts]
        }
        this.wechatAccounts = this.allAccounts.filter(account => account.Name.toLowerCase().indexOf(this.keyword.toLowerCase()) > -1) || []
        this.showLoading = false
      } else if (queryType === 'filter') {
        this.wechatAccounts = this.allAccounts.filter(account => account.Name.toLowerCase().indexOf(this.keyword.toLowerCase()) > -1) || []
      }
      this.addMaskName()
    })
    this.$bus.on('detail-close-dropdown', () => {
      this.show = false
    })
  },
  beforeDestroy () {
    this.$bus.off('open-dropdown')
    this.$bus.off('detail-close-dropdown')
    window.removeEventListener('click', this.hideListener)
  },
  methods: {
    openDetailModal () {
      this.show = false
      this.$bus.emit('open-detail-modal', this.keyword)
    },
    addMaskName () {
      const keyword = this.keyword ? this.keyword.toLowerCase().trim() : ''
      this.wechatAccounts.forEach((item) => {
        const name = item.name || item.Name
        const matchIndex = name.toLowerCase().indexOf(keyword)
        if (matchIndex > -1) {
          const rawHtml = [
            name.substring(0, matchIndex),
            '<mark>',
            name.substring(matchIndex, matchIndex + keyword.length),
            '</mark>',
            name.substring(matchIndex + keyword.length)
          ].join('')
          this.$set(item, 'rawHtml', rawHtml)
        }
      })
    },
    selectWeChatAccount (account) {
      this.show = false
      delete account.rawHtml
      this.$emit('selected', account)
      this.$bus.emit('close-dropdown', account)
    }
  }
}
</script>

<style lang="scss" scoped>
  .slds-icon-standard-wechat-account {
    background-color: #54698d;
  }
  .charket-lookup-dropdown {
    min-height: 40px;
  }
</style>
