const escape2HTML = (str) => {
  let arrEntities = { 'lt': '<', 'gt': '>', 'nbsp': ' ', 'amp': '&', 'quot': '"' }
  if (str != null) {
    str = str.replace(/&#39;/ig, "'")
    return str.replace(/&(lt|gt|nbsp|amp|quot);/ig, function (all, t) { return arrEntities[t] })
  }
}
class ErrorParser {
  constructor (error) {
    this.error = error
  }

  isSessionExpired () {
    return this.error instanceof Error &&
      this.error.name === 'SessionExpiredError'
  }

  isTimeout () {
    return this.error.result === null &&
      !this.error.statusCode &&
      this.error.xhr.isTimeout
  }

  isOffline () {
    return this.error.result === null &&
      !this.error.statusCode &&
      navigator.onLine === false // check navigator.onLine exactly equal false
  }

  parseMessage () {
    if (this.error instanceof Error) {
      return escape2HTML(this.error.message)
    }

    let message = ''
    if (this.error.message) {
      message = this.error.message
    } else if (this.error.error && this.error.error.message) {
      // todo: refactor, 找出那里会有 error.error 的情况，将其修复，然后去掉此处判断
      message = this.error.error.message
    } else if (this.error.error) {
      // todo: refactor, 找出那里会有 error.error 的情况，将其修复，然后去掉此处判断
      message = this.error.error
    }

    return escape2HTML(message)
  }
}

export default ErrorParser
