import Transport from './Transport'

const ACTION_BASE_NAME = 'MessageRemoteActions'

class MessageTransport extends Transport {
  constructor () {
    super(ACTION_BASE_NAME)
  }

  getWeChatMessages (messageIds) {
    return this.invoke('getWeChatMessages', [
      JSON.stringify(messageIds)
    ])
  }

  getImages (wechatAccountId, offset, limit, keyword) {
    return this.invoke('getImages', [
      wechatAccountId,
      offset,
      limit,
      keyword
    ])
  }

  getVideos (wechatAccountId, offset, limit, keyword) {
    return this.invoke('getVideos', [
      wechatAccountId,
      offset,
      limit,
      keyword
    ])
  }

  getVoices (wechatAccountId, offset, limit, keyword) {
    return this.invoke('getVoices', [
      wechatAccountId,
      offset,
      limit,
      keyword
    ])
  }

  getMiniProgramPages (wechatAccountId, offset, limit, keyword) {
    return this.invoke(
      'getMiniProgramPages',
      [ wechatAccountId, offset, limit, keyword ]
    )
  }

  getStructuredMessages (wechatAccountId, offset, limit, keyword, { structuredType = 'mpnews' }) {
    return this.invoke(
      'getStructuredMessages',
      [ wechatAccountId, offset, limit, keyword, structuredType ]
    )
  }

  getTextMessages (wechatAccountId, offset, limit, keyword) {
    return this.invoke(
      'getTextMessages',
      [ wechatAccountId, offset, limit, keyword ]
    )
  }

  getMiniProgramAppId (wechatAccountId) {
    return this.invoke(
      'getMiniProgramAppId',
      [ wechatAccountId ]
    )
  }

  saveTextMessage (wechatAccountId, content) {
    return this.invoke('saveTextMessage', [
      wechatAccountId,
      content
    ])
  }

  updateTextMessage (id, content) {
    return this.invoke('updateTextMessage', [
      content,
      id
    ])
  }
}

export default MessageTransport
