import Transport from './Transport'

const ACTION_BASE_NAME = 'WeChatAccountRemoteActions'

class WeChatAccountTransport extends Transport {
  constructor () {
    super(ACTION_BASE_NAME)
  }

  getAllWeChatAccounts () {
    return this.invoke('getAllWeChatAccounts')
  }

  deleteWeChatAccount (accountId) {
    return this.invoke('deleteWeChatAccount', [
      accountId
    ])
  }

  checkSetupStep (accountId) {
    return this.invoke('checkSetupStep', [
      accountId
    ])
  }

  syncWeChatData (accountId, options) {
    return this.invoke('syncWeChatData', [
      accountId,
      JSON.stringify(options)
    ])
  }

  getWeChatAccountSettings (wechatAccountId) {
    return this.invoke('getWeChatAccountSettings', [
      wechatAccountId
    ])
  }

  getTypes () {
    return this.invoke('getTypes')
  }

  getSites () {
    return this.invoke('getSites')
  }

  generateToken () {
    return this.invoke('generateToken')
  }

  createWeChatAccount (wechatAccountSettings) {
    return this.invoke('createWeChatAccount', [
      JSON.stringify(wechatAccountSettings)
    ])
  }

  updateWeChatAccount (wechatAccountSettings) {
    return this.invoke('updateWeChatAccount', [
      JSON.stringify(wechatAccountSettings)
    ])
  }

  saveQRCode (wechatAccountId, attachment) {
    return this.invoke('saveQRCode', [
      wechatAccountId,
      JSON.stringify(attachment)
    ])
  }

  checkSyncDataJobStatus (wechatAccountId) {
    return this.invoke('checkSyncDataJobStatus', [
      wechatAccountId
    ])
  }

  getLinkWeChatFollowerTo () {
    return this.invoke('getLinkWeChatFollowerTo', [])
  }

  updateLinkWeChatFollowerTo (wechatAccountId, linkWeChatFollowerTo, linkWeChatFollowerToRecordTypeId, isIdentifyWeChatFollowersByUnionIdsEnabled) {
    return this.invoke('updateLinkWeChatFollowerTo', [
      wechatAccountId,
      linkWeChatFollowerTo,
      linkWeChatFollowerToRecordTypeId,
      isIdentifyWeChatFollowersByUnionIdsEnabled
    ])
  }

  checkAutoRepliesAndMenuDataIntegrity (wechatAccountId) {
    return this.invoke('checkAutoRepliesAndMenuDataIntegrity', [
      wechatAccountId
    ])
  }

  generateActivatedQRCode (wechatAccountId) {
    return this.invoke('generateActivatedQRCode', [
      wechatAccountId
    ])
  }

  checkSetupDone (wechatAccountId) {
    return this.invoke('checkSetupDone', [
      wechatAccountId
    ])
  }

  getLinkMethods () {
    return this.invoke('getLinkMethods', [])
  }
}

export default WeChatAccountTransport
