<template>
  <div :style="{position: 'fixed', transform: 'none', left: position.left, top: position.top, width: position.width, zIndex: 10000}" @click.stop="" v-if="show">
    <ul class="slds-listbox slds-listbox_vertical slds-dropdown slds-dropdown_fluid dropdown-list" role="presentation">
      <li role="presentation" class="slds-listbox__item" v-for="(option, index) in options" :key="index" @click="selectOption(option)">
        <div class="slds-media slds-listbox__option slds-listbox__option_plain slds-media_small slds-media_center" :class="{'slds-is-selected': option.Id === current.Id}" role="option">
          <span class="slds-media__figure">
            <icon name="check" icon-class-name="slds-icon slds-icon_x-small slds-listbox__icon-selected">
            </icon>
          </span>
          <span class="slds-media__body">
            <span class="slds-truncate" :title="option.Name">{{ option.Name }}</span>
          </span>
        </div>
      </li>
      <li role="presentation" class="slds-listbox__item last-item" v-if="fixedValue.Name" @click="selectOption(fixedValue)">
        <div class="slds-media slds-listbox__option slds-listbox__option_plain slds-media_small slds-media_center" :class="{'slds-is-selected': current.Name === fixedValue.Name}" role="option">
          <span class="slds-media__figure">
            <icon name="check" icon-class-name="slds-icon slds-icon_x-small slds-listbox__icon-selected">
            </icon>
          </span>
          <span class="slds-media__body">
            <span class="slds-truncate" :title="fixedValue.Name">{{ fixedValue.Name }}</span>
          </span>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>

export default {
  name: 'PicklistDropdown',
  props: {
    options: {
      type: Array,
      default () {
        return []
      }
    },
    current: {
      type: Object,
      default () {
        return {}
      }
    },
    fixedValue: {
      type: Object,
      default () {
        return {}
      }
    }
  },
  data () {
    return {
      show: false,
      position: {},
      clickListener: null
    }
  },
  created () {
    this.$nextTick(function () {
      this.clickListener = () => {
        this.show = false
        this.$bus.emit('close-picklist-dropdown')
      }
      window.addEventListener('click', this.clickListener)
    })
  },
  mounted () {
    this.$bus.on('open-picklist-dropdown', (valueObj) => {
      this.show = true
      this.position = valueObj
    })
  },
  beforeDestroy () {
    this.$bus.off('open-picklist-dropdown')
    window.removeEventListener('click', this.clickListener)
  },
  methods: {
    selectOption (option) {
      this.show = false
      this.$bus.emit('close-picklist-dropdown')
      this.$emit('select', option)
    }
  }
}
</script>

<style scoped lang="scss">
  .last-item {
    border-top: 1px solid #D8D8D8;
  }
</style>
