export default {
  data () {
    return {
      showError: false
    }
  },
  methods: {
    checkNum (e, num, hasZero = false) {
      let reg
      if (num === '0') {
        e.returnValue = e.keyCode === 8
      } else {
        if (!num) {
          reg = hasZero ? /^[0-9]$/ : /^[1-9]$/
        } else {
          reg = /\d/
        }
        e.returnValue = (reg.test(e.key) || e.keyCode === 8)
      }
    },
    limitNum (num, maxValue) {
      this.showError = num && num > maxValue
    }
  }
}
