import { formatDatetimeFilter } from '../../filters/text'
import { DataProvider } from './data-providers'

const ALL_CHOOSERS = [
  'Image',
  'Video',
  'Voice Message',
  'Text Message',
  'Structured Message'
]

const TYPE_LABEL_MAPPING = new Map([
  ['Image', 'Image'],
  ['Video', 'Video'],
  ['Voice Message', 'Voice Clip'],
  ['Text Message', 'Text'],
  ['Structured Message', 'Structured Message']
])

const TYPE_SELECT_LABEL_MAPPING = new Map([
  ['Image', 'Select Image'],
  ['Video', 'Select Video'],
  ['Voice Message', 'Select Voice Clip'],
  ['Text Message', 'Select Text Message'],
  ['Structured Message', 'Select Structured Message']
])

class WechatResponseHelper {
  constructor (componentInstance) {
    this.componentInstance = componentInstance
  }

  static getAllChoosers () {
    return [...ALL_CHOOSERS]
  }

  getReplyModeConfig (enabled) {
    if (enabled === 'false' || enabled === false || enabled === null || enabled === undefined) {
      return
    }

    return {
      label: 'Reply Mode',
      modes: [
        { label: 'Random', value: 'Random' }
      ]
    }
  }

  getChooserConfigs (choosers) {
    return choosers.map((type) => {
      const config = {
        type,
        name: TYPE_LABEL_MAPPING.get(type),
        layout: this.createLayout(type),
        dataProvider: this.createDataProvider(type)
      }

      if (type === 'Text Message') {
        config.configs = this.getSubConfigsOfTextMessageChooser()
      }

      return config
    })
  }

  getSubConfigsOfTextMessageChooser () {
    return {
      designer: {
        link: {
          iconTitle: 'Add link',
          title: 'Title',
          linkURL: 'Link URL',
          invalidUrl: 'Invalid URL',
          primaryButton: 'OK',
          secondaryButton: 'Cancel',
          change: 'Change',
          remove: 'Remove'
        },
        miniProgramPage: {
          iconTitle: 'Add mini program link',
          title: 'Title',
          linkUrl: 'Link URL',
          invalidUrl: 'Invalid URL',
          appId: 'App ID',
          pagePath: 'Page Path',
          primaryButton: 'OK',
          secondaryButton: 'Cancel',
          change: 'Change',
          remove: 'Remove',
          dataProvider: this.createDataProvider('Mini Program Link')
        },
        action: {
          iconTitle: 'Add action',
          name: 'Name',
          value: 'Value',
          primaryButton: 'OK',
          secondaryButton: 'Cancel'
        },
        mergeField: {
          iconTitle: 'Insert merge field',
          title: 'Select A Field',
          placeholder: 'Find An Object...',
          filterPlaceholder: 'Type here to filter the list...',
          fieldTitle: 'You have selected the following field',
          type: 'Type',
          apiName: 'API Name',
          primaryButton: 'Choose',
          secondaryButton: 'Cancel',
          noMatches: 'No matches found',
          currentSelection: 'Current Selection',
          dataProvider: this.createDataProvider('Merge Field')
        }
      },
      editor: {
        title: 'Edit Text Message',
        cancelButton: 'Cancel',
        okButton: 'Save'
      }
    }
  }

  createLayout (type) {
    const layout = {
      title: TYPE_SELECT_LABEL_MAPPING.get(type),
      noRecords: 'No Records',
      editButton: 'global.editBtn',
      searchPlaceholder: 'Search this list...',
      primaryButton: 'OK',
      secondaryButton: 'Cancel'
      // more layouts
    }

    switch (type) {
      case 'Text Message':
        layout.tabs = {
          new: {
            title: 'Create Text Message',
            tabLabel: 'New Text Message'
          },
          list: {
            title: 'Select Text Message',
            tabLabel: 'Text Messages'
          }
        }
        layout.iconLabel = 'Text'
        break

      case 'Image':
        layout.uploader = {}
        layout.iconLabel = 'Image'
        break

      case 'Voice Message':
        layout.uploader = {}
        layout.iconLabel = 'Voice Clip'
        break

      case 'Video':
        layout.uploader = {}
        layout.iconLabel = 'Video'
        break

      case 'Structured Message':
        layout.iconLabel = 'Structured Message'
        layout.syncedTooltip = 'Synced with WeChat'
        break

      default:
        break
    }

    return layout
  }

  createDataProvider (type) {
    const options = {
      wechatAccount: this.componentInstance.wechatAccount,
      pageName: this.componentInstance.pageName,
      enableMergeField: this.componentInstance.enableMergeField
    }

    switch (type) {
      case 'Structured Message':
        options.formatter = (data) => {
          data.localizedWeChatLastModifiedTime = formatDatetimeFilter(data.weChatLastModifiedTime, this.componentInstance.$root.global_user_timezone)
          return data
        }
        break

      case 'Voice Message':
      case 'Video':
        options.formatter = (data) => {
          data.localizedUpdateDateTime = formatDatetimeFilter(data.updateDateTime, this.componentInstance.$root.global_user_timezone)
          return data
        }
        break

      default:
        break
    }

    return DataProvider.create(type, options)
  }
}

export default WechatResponseHelper
