<template>
  <ld-modal
    ref="Modal"
    :title="modal.header"
  >
    <template #content>
      <div class="slds-p-around_medium modal-content-section">
        <div class="slds-form-element" :class="{'slds-has-error': showRequiredError}">
          <label class="slds-form-element__label" for="status-name">
            <abbr class="slds-required" title="required">* </abbr>Name</label>
          <div class="slds-form-element__control">
            <input id="status-name" class="slds-input" type="text" v-model="agent.Name"/>
          </div>
          <div class="slds-form-element__help" v-if="showRequiredError">This field is required</div>
        </div>
        <div class="slds-form-element">
          <div class="slds-form-element__control">
            <div class="slds-checkbox checkbox-group-item">
              <input type="checkbox" name="options" id="accepts-chats" v-model="agent.IsAcceptsChats"/>
              <label class="slds-checkbox__label" for="accepts-chats">
                <span class="slds-checkbox_faux"></span>
                <span class="slds-form-element__label cursor-label">Accepts Chats</span>
              </label>
            </div>
            <div class="slds-checkbox checkbox-group-item overridable-section">
              <input type="checkbox" name="options" id="overridable-by-system" v-model="agent.IsNonoverridableBySystem"/>
              <label class="slds-checkbox__label" for="overridable-by-system">
                <span class="slds-checkbox_faux"></span>
                <span class="slds-form-element__label cursor-label">Non-Overridable By System</span>
              </label>
              <tooltip info-style="top: -1px; left: 190px;" content="An agent choosing this status will remain as the status without being affected by system default behaviors. Such status is usually used by the Charket mobile app."></tooltip>
            </div>
            <div class="slds-checkbox checkbox-group-item-last">
              <input type="checkbox" name="options" id="active" :disabled="isDefaultStatus" v-model="agent.IsActive"/>
              <label class="slds-checkbox__label" for="active">
                <span class="slds-checkbox_faux"></span>
                <span class="slds-form-element__label cursor-label">Active</span>
              </label>
            </div>
          </div>
        </div>
      </div>
    </template>

    <template #footer>
      <ld-button
        @click="hideModal()"
        neutral
      >
        Cancel
      </ld-button>
      <ld-button
        :disabled="isSaving"
        @click="saveAgent()"
        brand
      >
        Save
      </ld-button>
    </template>
  </ld-modal>
</template>

<script>
import { createAgentStatus, updateAgentStatus } from '../../api/charket-settings'
import { LdModal } from 'charket-ui/dist/ld-modal.js'
import { LdButton } from 'charket-ui/dist/ld-button.js'

export default {
  name: 'NewAgentStatus',
  props: {
    editAgent: {
      type: Object,
      default () {
        return {}
      }
    }
  },
  components: {
    LdModal,
    LdButton
  },
  data () {
    return {
      agent: {
        Name: '',
        IsAcceptsChats: false,
        IsNonoverridableBySystem: false,
        IsActive: true
      },
      isSaving: false,
      modal: {
        header: 'New Agent Status'
      },
      isDefaultStatus: false,
      showRequiredError: false
    }
  },
  watch: {
    'editAgent.Id' () {
      if (this.editAgent.Id) {
        this.modal.header = 'Edit Agent Status'
        this.agent.Id = this.editAgent.Id
        this.agent.Name = this.editAgent.Name
        this.agent.IsAcceptsChats = this.editAgent.IsAcceptsChats
        this.agent.IsNonoverridableBySystem = this.editAgent.IsNonoverridableBySystem
        this.agent.IsActive = this.editAgent.IsActive
        this.isDefaultStatus = this.editAgent.IsOfflineDefaultStatus
      } else {
        this.modal.header = 'New Agent Status'
        this.agent = {
          Name: '',
          IsAcceptsChats: false,
          IsNonoverridableBySystem: false,
          IsActive: true
        }
        this.isDefaultStatus = false
      }
    }
  },
  created () {
    setTimeout(() => {
      this.$bus.emit('hide-loading')
    }, 0)
  },
  methods: {
    open () {
      this.$refs.Modal.open()
    },
    hideModal () {
      this.showRequiredError = false
      this.$emit('hide')
      this.$refs.Modal.close()
    },
    async saveAgent () {
      if (this.agent.Name) {
        this.isSaving = true
        if (this.editAgent.Id) {
          try {
            await updateAgentStatus(JSON.stringify(this.agent))
            this.$bus.emit('success', { content: 'Agent status was saved.', type: 'success' })
            this.$emit('save')
            this.$refs.Modal.close()
          } catch (error) {
            this.handleError(error)
          } finally {
            this.isSaving = false
            this.showRequiredError = false
          }
        } else {
          try {
            let resultData = await createAgentStatus(JSON.stringify(this.agent))
            this.$bus.emit('success', { content: 'Agent status was created.', type: 'success' })
            this.$router.push({ path: `/agent-statuses/agent-statuses-settings/${resultData.Id}/detail` })
          } catch (error) {
            this.handleError(error)
          } finally {
            this.isSaving = false
            this.showRequiredError = false
          }
        }
      } else {
        this.showRequiredError = true
      }
    },
    handleError (e) {
      const errorMessage = this.$parseErrorMessage(e)
      this.$refs.modal.showError(errorMessage)
    }
  }
}
</script>

<style lang="scss" scoped>
  .checkbox-group-item {
    margin-top: 18px;
  }
  .checkbox-group-item-last {
    margin-top: 18px;
    margin-bottom: 108px;
  }
  .modal-content-section {
    padding: 30px 42px 16px 42px;
  }
  .cursor-label {
    cursor: pointer;
  }
  .overridable-section {
    position: relative;
  }
</style>
