import moment from 'moment-timezone'

const dateFormat = {
  date: 'M/D/YYYY',
  datetime: 'M/D/YYYY h:mm A',
  time: 'h:mm A'
}

export const filterImg = function (value) {
  const defaultImg = require('../assets/images/error_img.png')
  if (!value) {
    return defaultImg
  } else if (typeof value === 'string' && (value.indexOf('?file=undefined') > -1 || value.indexOf('?file=null') > -1)) {
    return defaultImg
  } else {
    return value
  }
}

export const addSeparators = function (value) {
  const num = (value || 0).toString()
  const temp = num.split('.')
  const integerNum = temp[0]
  const decimalsNum = temp[1] ? '.' + temp[1] : ''
  if (parseInt(integerNum) > 0) {
    return integerNum.replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1,') + decimalsNum
  } else {
    return num
  }
}

export function formatDatetimeFilter (datetime, timeZoneSidKey) {
  if (datetime) {
    const momentObj = moment.tz(datetime, timeZoneSidKey)
    return momentObj.format(dateFormat['datetime'])
  } else {
    return datetime
  }
}

export const lowercase = function (value) {
  return value && typeof value === 'string' ? value.toLowerCase() : ''
}

export const capitalize = function (value) {
  let capitalized = ''
  if (value && typeof value === 'string') {
    const words = value.split(' ')
    capitalized = words.map((word) => {
      if (word === '') {
        return ''
      }

      const chars = Array.from(word)
      chars.splice(0, 1, word.charAt(0).toUpperCase())

      return chars.join('')
    }).join(' ')
  }

  return capitalized
}
