<template>
  <table class="slds-table slds-table_bordered slds-table--header-fixed handlers-table">
    <thead>
      <tr class="slds-line-height_reset">
        <th class="slds-cell-wrap thead__space" scope="col">
          <span class="slds-truncate slds-cell-fixed"></span>
        </th>
        <th class="slds-cell-wrap thead__order" scope="col">
          <span class="slds-grid slds-grid_vertical-align-center slds-cell-fixed thead__order">
            <span class="slds-truncate" title="Execute Order">Execute Order</span>
          </span>
        </th>
        <th class="slds-cell-wrap thead__apex" scope="col">
          <span class="slds-grid slds-grid_vertical-align-center slds-cell-fixed thead__apex">
            <span class="slds-truncate" title="Apex Class">Apex Class</span>
          </span>
        </th>
        <th class="slds-cell-wrap" scope="col">
        </th>
      </tr>
    </thead>
    <draggable
      v-model="draggableHandlers"
      element="tbody"
      @end="dragEnd"
    >
      <tr
        class="slds-hint-parent handler-item"
        v-for="(handler, index) in draggableHandlers"
        :key="index"
      >
        <th>
          <span class="slds-media__figure slds-listbox__option-icon handler-item__drag-icon">
            <span class="slds-icon_container slds-icon-utility-search">
              <icon name="drag_drop" icon-class-name="slds-icon slds-icon_x-small slds-icon-text-default">
              </icon>
            </span>
          </span>
        </th>
        <th data-label="EXECUTE ORDER" scope="row">
          <div class="slds-truncate">{{ handler.Order }}</div>
        </th>
        <td data-label="APEX CLASS">
          <lookup
            ref="Lookup"
            class="handler-item__lookup"
            :ld-selected-item="getApexLookupItem(handler.Name)"
            :ld-metadatas="metadata"
            :condition="condition"
            @select="onSelectObject($event, handler)"
            @remove="onSelectedObjectRemoved(handler)"
          />
        </td>
        <td data-label="ACTION" class="handler-item__button-group">
          <div class="slds-button-group" role="group">
            <ld-button
              class="slds-button_icon-more"
              @click="onClickDash(index)"
            >
              <ld-button-svg-icon slot="icon" content="#dash" />
            </ld-button>
            <ld-button
              class="slds-button_icon-more"
              @click="onClickAdd()"
            >
              <ld-button-svg-icon slot="icon" content="#add" />
            </ld-button>
          </div>
        </td>
      </tr>
    </draggable>
  </table>
</template>

<script>
import { LdButton, LdButtonSvgIcon } from 'charket-ui/dist/ld-button.js'
import draggable from 'vuedraggable'
import Lookup from '@/components/Lookup/Lookup.vue'
import config from '../../config'

const NS = config.sfdc_namespace || ''

export default {
  props: {
    handlers: {
      type: Array,
      default () {
        return []
      }
    },
    type: {
      type: String,
      default () {
        return 'Message'
      }
    },
    executionOrder: {
      type: String,
      default () {
        return 'Before'
      }
    }
  },
  components: {
    LdButton,
    LdButtonSvgIcon,
    draggable,
    Lookup
  },
  data () {
    return {
      draggableHandlers: [],
      metadata: {
        name: 'ApexClass',
        label: 'Apex',
        labelPural: 'Apex Classes',
        icon: 'apex',
        linkField: {
          name: 'Name',
          label: 'Apex Class'
        },
        fields: []
      },
      defaultHandler: {
        Order: 1,
        ApexClassId: null,
        Name: null,
        ExecutionOrder: this.executionOrder,
        Type: this.type
      }
    }
  },
  computed: {
    condition () {
      return NS ? `NamespacePrefix != '${NS}'` : null
    }
  },
  created () {
    if (this.handlers.length === 0) {
      this.draggableHandlers.push(JSON.parse(JSON.stringify(this.defaultHandler)))
      return
    }

    this.draggableHandlers = JSON.parse(JSON.stringify(this.handlers))
  },
  methods: {
    getApexLookupItem (name) {
      return name
        ? { Name: name }
        : null
    },
    dragEnd () {
      this.draggableHandlers.map((item, index) => {
        if ((index + 1) !== item.Order) {
          item.Order = index + 1
        }
      })
      this.$emit('changed', this.draggableHandlers)
    },
    onSelectObject (event, handler) {
      this.draggableHandlers.forEach(item => {
        if (item.Order === handler.Order) {
          item.Name = event.name
          item.ApexClassId = event.id
        }
      })
      this.$emit('changed', this.draggableHandlers)
    },
    onSelectedObjectRemoved (handler) {
      this.draggableHandlers.forEach(item => {
        if (item.Order === handler.Order) {
          item.Name = null
          item.ApexClassId = null
        }
      })
      this.$emit('changed',
        this.draggableHandlers.length === 1
          ? []
          : JSON.parse(JSON.stringify(this.draggableHandlers))
      )
    },
    onClickAdd () {
      this.draggableHandlers.push({
        Order: this.draggableHandlers.length + 1,
        ApexClassId: null,
        Name: null,
        ExecutionOrder: this.executionOrder,
        Type: this.type
      })
      this.$emit('changed', this.draggableHandlers)
    },
    onClickDash (index) {
      this.draggableHandlers.splice(index, 1)
      this.draggableHandlers.forEach((item, index) => {
        item.Order = index + 1
      })
      this.$emit('changed', JSON.parse(JSON.stringify(this.draggableHandlers))
      )
      if (this.draggableHandlers.length === 0) {
        this.draggableHandlers.push(JSON.parse(JSON.stringify(this.defaultHandler)))
      }
    }
  }
}
</script>

<style lang="scss" scope>
  .handlers-table {
    table-layout: fixed;
    .thead {
      &__space {
        width: 3rem;
      }
      &__order {
        width: 110px;
      }
      &__apex {
        min-width: 210px;
        max-width: 370px;
      }
    }
    .handler-item {
      cursor: pointer;
      &__lookup {
        min-width: 200px;
        max-width: 360px;
      }
      &__button-group {
        text-align: right;
        padding-right: 16px!important;
      }
      &__drag-icon {
        cursor: move;
        margin-bottom: 2px;
      }
    }
    .slds-cell-fixed {
      padding: .25rem .5rem;
    }
  }
</style>
