<template>
  <div v-if="show">
    <section role="dialog" tabindex="-1" aria-labelledby="modal-heading-01" aria-modal="true" aria-describedby="modal-content-id-1" class="slds-modal slds-modal_large slds-fade-in-open">
      <div class="slds-modal__container">
        <header class="slds-modal__header">
          <button class="slds-button slds-button_icon slds-modal__close slds-button_icon-inverse" title="Close" @click="closeModal()">
            <icon name="close" icon-class-name="slds-button__icon slds-button__icon_large">
            </icon>
            <span class="slds-assistive-text">Close</span>
          </button>
          <h2 class="slds-text-heading_medium slds-hyphenate">WeChat Account Results</h2>
        </header>
        <div class="slds-modal__content slds-p-around_medium advanced-modal-content">
          <div class="modal-input-box">
            <input-box :keyword="keyword" refs="advancedLookup">
            </input-box>
          </div>
          <div class="slds-page-header page-header-space">
            <div class="slds-page-header__row">
              <div class="slds-page-header__col-title">
                <div class="slds-media">
                  <div class="slds-media__body">
                    <div class="slds-page-header__name">
                      <div class="slds-page-header__name-title">
                        <h1>
                          <span class="slds-page-header__title slds-truncate" title="Rohde Corp - 80,000 Widgets">WeChat Accounts</span>
                        </h1>
                      </div>
                    </div>
                    <p class="slds-page-header__name-meta">{{ resultMsg }}</p>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <loading v-if="showLoading">
          </loading>
          <table class="slds-table slds-table_cell-buffer slds-table_bordered" v-if="wechatAccounts.length > 0" key="wechat-accounts-list">
            <tbody>
              <tr class="slds-hint-parent">
                <th data-label="Name" scope="row">
                  <div class="slds-truncate" title="Name"><a href="javascript:void(0);" tabindex="-1">Name</a></div>
                </th>
                <td>
                  <div class="slds-truncate">Type</div>
                </td>
                <td>
                  <div class="slds-truncate">IsVerified</div>
                </td>
              </tr>
              <tr class="slds-hint-parent" v-for="(account, index) in wechatAccounts" :key="index" @click="selectAccount(account)">
                <th data-label="Name" scope="row">
                  <div class="slds-truncate" title="Name"><a href="javascript:void(0);" tabindex="-1">{{ account.Name }}</a></div>
                </th>
                <td data-label="Type">
                  <div class="slds-truncate" title="Type">{{ account.Type__c }}</div>
                </td>
                <td data-label="Verified">
                  <div class="slds-form-element">
                    <div class="slds-form-element__control">
                      <div class="slds-checkbox">
                        <input type="checkbox" name="options" id="checkbox" disabled :checked="account.IsVerified"/>
                        <label class="slds-checkbox__label" for="checkbox">
                          <span class="slds-checkbox_faux"></span>
                        </label>
                      </div>
                    </div>
                  </div>
                </td>
              </tr>
            </tbody>
          </table>
          <div class="slds-text-color_default" v-else key="wechat-accounts-list">No results for {{ keyword }} in WeChat Accounts</div>
        </div>
        <footer class="slds-modal__footer">
          <button class="slds-button slds-button_neutral" @click="closeModal()">Cancel</button>
        </footer>
      </div>
    </section>
    <div class="slds-backdrop slds-backdrop_open"></div>
  </div>
</template>

<script>
import InputBox from './InputBox'
import LookupMixin from './LookupMixin'

export default {
  name: 'AdvancedModal',
  components: {
    InputBox
  },
  mixins: [LookupMixin],
  props: {
    isShow: {
      type: Number,
      default () {
        return 0
      }
    }
  },
  data () {
    return {
      keyword: '',
      show: false,
      showLoading: false,
      currentItem: {}
    }
  },
  computed: {
    resultMsg () {
      let wechatAccountLen = this.wechatAccounts.length
      if (wechatAccountLen <= 0) {
        return '0 Results'
      } else if (wechatAccountLen === 1) {
        return '1 Result '
      } else if (wechatAccountLen > 1) {
        return `${wechatAccountLen} Results • Sorted by relevancy`
      }
    }
  },
  watch: {
    'isShow' () {
      this.show = false
    }
  },
  mounted () {
    this.$bus.on('open-detail-modal', async (searchKey) => {
      this.keyword = searchKey
      this.show = true
      this.showLoading = true
      await this.fetchWeChatAccounts(this.keyword)
      this.showLoading = false
    })
  },
  beforeDestroy () {
    this.$bus.off('open-detail-modal')
  },
  methods: {
    closeModal () {
      this.show = false
    },
    selectAccount (account) {
      this.show = false
      this.$emit('selected', account)
    }
  }
}
</script>

<style lang="scss" scoped>
  .advanced-modal-content {
    position: relative;
    min-height: 400px;
  }
  .modal-input-box {
    max-width: 438px;
  }
  .page-header-space {
    margin-top: 10px;
    margin-bottom: 10px;
    background: #fff;
  }
</style>
