import { TransporterCreator, TransportBase } from 'charket-transport'
import { SFEnvironment } from '../lib'

const transporter = TransporterCreator.create({ namespace: SFEnvironment.current.charketNamespace, env: 'Visualforce' })
class Transport extends TransportBase {
  constructor (actionName) {
    super(actionName, transporter)
  }
}

export default Transport
