const checkVerificationCodeSettingsRouterPermission = () => {
  return new Promise((resolve, reject) => {
    let checkCount = 0
    if (typeof store.state.charketSms.smsIntegrationSettings === 'undefined') {
      const timer = setInterval(() => {
        checkCount++
        if (checkCount >= 10) {
          resolve(false)
          clearInterval(timer)
        }
        if (typeof store.state.charketSms.smsIntegrationSettings === 'undefined') {
          return
        }

        const isCharketSmsEnabled = store.state.charketSms.smsIntegrationSettings.IsCharketSmsEnabled
        if (isCharketSmsEnabled) {
          resolve(true)
        } else {
          resolve(false)
        }
        clearInterval(timer)
      }, 500)
    } else {
      resolve(store.state.charketSms.smsIntegrationSettings.IsCharketSmsEnabled)
    }
  })
}

router.beforeEach(async (to, from, next) => {
  let pageName = to.name

  if (!pageName) {
    next({ path: '/' })
  } else if (pageName === 'VerificationCodeSettings') {
    const permission = await checkVerificationCodeSettingsRouterPermission()
    if (permission) {
      next()
    } else {
      next({ path: '/' })
    }
  } else {
    next()
  }
})

export default router
