import { SObjectService } from '../../../service'

class MergeFieldDataProvider {
  constructor (options) {
    this.options = options
  }

  getObjects () {
    return SObjectService.getObjectsByPageName(this.options.pageName)
  }

  getFields (objectName) {
    return SObjectService.getFields(objectName)
  }
}

export default MergeFieldDataProvider
