import config from '../config'
const namespace = config.sfdc_namespace
class SObject {
  static getQualifiedName (name) {
    if (!SObject.isCustomObject(name) || SObject.hasNamespace(name)) {
      return name
    }

    return namespace
      ? `${namespace}__${name}`
      : name
  }

  static isCustomObject (name) {
    return name.endsWith('__c')
  }

  static getQualifiedField (name) {
    if (!SObject.isCustomField(name) || SObject.hasNamespace(name)) {
      return name
    }

    return namespace
      ? `${namespace}__${name}`
      : name
  }

  static hasNamespace (name) {
    if (!SObject.isCustomField(name)) {
      return false
    }

    return name.replace('__c', '').includes('__')
  }

  static isCustomField (name) {
    return name.endsWith('__c')
  }
}

export default SObject
