import { QueryTransport } from '../transport'
import { SObject } from '../salesforce'
import { Record } from '../model'

const transport = new QueryTransport()

class QueryService {
  static async getRecentItems (objectName, condition = null) {
    const qualifiedObjectName = SObject.getQualifiedName(objectName)

    const items = await transport.getRecentlyViewedRecords(qualifiedObjectName, condition)
    return items.map((item) => {
      return new Record(item)
    })
  }

  static async searchRecords (keyword, objectName, condition = null, orderBy = null) {
    const qualifiedObjectName = SObject.getQualifiedName(objectName)

    const items = await transport.searchRecords(keyword, qualifiedObjectName, condition, orderBy)
    return items.map((item) => {
      return new Record(item)
    })
  }

  static async searchRecordsWithLayoutFields (keyword, objectName, condition = null, orderBy = null) {
    const qualifiedObjectName = SObject.getQualifiedName(objectName)
    const {
      Columns: columns,
      Records: records
    } = await transport.searchRecordsWithLayoutFields(keyword, qualifiedObjectName, condition, orderBy)

    return {
      columns: (columns || []).map((item) => {
        return new Record(item)
      }),
      records: records.map((item) => {
        return new Record(item)
      })
    }
  }
}

export default QueryService
