<template>
  <div>
    <div :style="infoStyle" class="tooltip-info" @mouseover="onMouseover($event)" @mouseout="onMouseout()">
      <div class="slds-form-element">
        <div class="slds-form-element__icon slds-align-middle">
          <button class="slds-button slds-button_icon slds-button slds-button_icon" aria-describedby="help">
            <icon name="info" icon-class-name="slds-button__icon">
            </icon>
          </button>
        </div>
      </div>
    </div>
    <div :style="{left: infoTooltipPosition.left, top: infoTooltipPosition.top}" class="slds-popover slds-popover_tooltip slds-nubbin_bottom-left tooltip" role="tooltip" v-if="showInfo">
      <div class="slds-popover__body">{{ content }}</div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Tooltip',
  props: {
    infoStyle: {
      type: String,
      default () {
        return ''
      }
    },
    content: {
      type: String,
      default () {
        return ''
      }
    }
  },
  data () {
    return {
      showInfo: false,
      infoTooltipPosition: {
        left: '0px',
        top: '0px'
      }
    }
  },
  methods: {
    onMouseover (event) {
      this.showInfo = true
      this.$nextTick(() => {
        let infoIconPosition = event.target.getBoundingClientRect()
        let tooltipEle = document.querySelector('.tooltip')
        // 16 is the tooltip left border
        // 10 is the arrow height
        this.infoTooltipPosition.left = `${infoIconPosition.left - 16}px`
        this.infoTooltipPosition.top = `${infoIconPosition.top - tooltipEle.clientHeight - 10}px`
      })
    },
    onMouseout () {
      this.showInfo = false
    }
  }
}
</script>
<style lang="scss" scoped>
  .tooltip-info {
    position: absolute;
    top: 2px;
    right: 2px;
    width: 14px;
    height: 14px;
  }
  .tooltip {
    position: fixed;
    // width: 298px;
    // height: 90px;
    display: flex;
    align-items: center;
    line-height: 20px;
    text-transform: initial;
  }
</style>
