<template>
  <div v-if="showDropdown" :class="{'slds-is-open': showMenu}" class="slds-dropdown_trigger slds-dropdown-trigger_click slds-align-middle slds-m-left_xx-small slds-shrink-none" id="charket-lookup-dropdown">
    <template v-if="currentItem.name === 'Case'">
      <icon :name="currentItem.icon" width="24px" height="24px" :class="'slds-icon-standard-' + currentItem.name.toLowerCase()" class="slds-icon slds-icon_small dropdown-icon"></icon>
    </template>
    <template v-if="currentItem.name !== 'Case'">
      <icon :name="currentItem.icon" width="24px" height="24px" :class="'slds-icon-standard-' + currentItem.icon" class="slds-icon slds-icon_small dropdown-icon"></icon>
    </template>
    <button :id="btnId" @click="showMenu = !showMenu" class="slds-button slds-button_icon slds-button_icon-small" aria-haspopup="true" style="box-shadow: none;">
      <icon name="down" width="16px" height="16px" class="slds-button__icon slds-icon-text-default" charket-icon></icon>
      <span class="slds-assistive-text">Filter by object</span>
    </button>
    <div class="slds-dropdown slds-dropdown_left">
      <ul class="slds-dropdown__list" role="menu">
        <li v-for="(obj, index) in objects" @click="select(obj)" class="slds-dropdown__item" role="presentation" :key="index">
          <a href="javascript:void(0);" role="menuitem" tabindex="-1">
            <span class="slds-truncate" v-if="obj.name !== 'Case'">
              <icon :name="obj.icon" :class="'slds-icon-standard-' + obj.icon" class="slds-icon slds-icon_small slds-m-right_small dropdown-icon" width="24px" height="24px"></icon>
              {{obj.labelPural}}
            </span>
            <span class="slds-truncate" v-if="obj.name === 'Case'">
              <icon :name="obj.icon" :class="'slds-icon-standard-' + obj.name.toLowerCase()" class="slds-icon slds-icon_small slds-m-right_small dropdown-icon" width="24px" height="24px"></icon>
              {{obj.labelPural}}
            </span>
          </a>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
const uniqueId = () => {
  const id = Math.random().toString(36).slice(4)
  return id
}

export default {
  data () {
    return {
      showMenu: false,
      currentItem: {
        icon: ''
      },
      btnId: uniqueId()
    }
  },
  props: {
    objects: {
      default () {
        return null
      }
    },
    current: {
      type: Object,
      default () {
        return null
      }
    }
  },
  computed: {
    'showDropdown': function () {
      return this.objects.length > 1
    }
  },
  mounted () {
    this.currentItem = this.current
    const dropdownBtn = document.getElementById(this.btnId)
    document.addEventListener('click', (event) => {
      if (dropdownBtn && !dropdownBtn.contains(event.target)) {
        this.showMenu = false
      }
    })
  },
  watch: {
    current (newValue) {
      this.currentItem = newValue
    }
  },
  methods: {
    select (obj) {
      if (this.currentItem !== obj) {
        this.currentItem = obj
        this.$emit('select', obj)
      }
    }
  }
}
</script>

<style scoped lang="scss">
  #charket-lookup-dropdown {
    .dropdown-icon {
      padding-bottom: 5px;
      padding-top: 3px;
    }
  }
</style>
