import { CharketSettingsTransport } from '../transport'

const transport = new CharketSettingsTransport()

class CharketSettingsService {
  static async getSFDCServerIPAddress () {
    return transport.getSFDCServerIPAddress()
  }

  static async getCustomHandlers (type, executionOrder) {
    return transport.getCustomHandlers(type, executionOrder)
  }

  static async saveCustomHandlers (type, executionOrder, handlers) {
    return transport.saveCustomHandlers(type, executionOrder, handlers)
  }

  static getChatDataSettings () {
    return transport.getChatDataSettings()
  }

  static saveChatDataSettings (settings) {
    return transport.saveChatDataSettings(settings)
  }
}

export default CharketSettingsService
