<template>
  <div class="list-view">
    <div class="slds-grid slds-grid_vertical-align-center slds-border_bottom list-view__info">
      <icon name="info" icon-class-name="info-icon"></icon>
      <span class="list-view__info--tip">{{ tip }}</span>
    </div>

    <div class="list-view__content">
      <div
        class="list-view__loading"
        v-show="isLoading"
      >
        <ld-spinner color="brand"></ld-spinner>
      </div>

      <div class="slds-table--header-fixed_container hanlders-list-content" key="handlers" v-if="!isLoading">
        <div class="slds-scrollable_y scroll-content">
          <handlers-table
            :handlers="handlers"
            :type="type"
            :execution-order="executionOrder"
            @changed="onChanged"
          ></handlers-table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { LdSpinner } from 'charket-ui/dist/ld-spinner.js'
import HandlersTable from './HandlersTable.vue'
import { CharketSettingsService } from '@/service'

export default {
  components: {
    LdSpinner,
    HandlersTable
  },
  props: {
    type: {
      type: String,
      default () {
        return 'Message'
      }
    },
    executionOrder: {
      type: String,
      default () {
        return 'Before'
      }
    }
  },
  data () {
    return {
      isLoading: true,
      handlers: []
    }
  },
  computed: {
    tip () {
      return this.executionOrder === 'Before'
        ? `A before handler can have callouts and DML operations, and it is executed before the Charket default handler.`
        : `An after handler can have DML operations but not callouts, and it's executed after the Charket default handler.`
    }
  },
  created () {
    this.load()
  },
  methods: {
    async load () {
      this.isLoading = true
      await this.$try(async () => {
        this.handlers = await CharketSettingsService.getCustomHandlers(this.type, this.executionOrder)
        this.handlers.sort((a, b) => a.Order - b.Order)
      })
      this.isLoading = false
    },
    onChanged (handlers) {
      this.$emit('changed', handlers)
    }
  }
}
</script>

<style lang="scss" scoped>
.list-view {
  height: 100%;
  &__info {
    padding: 13px;
    background-color: #fff;
    &--tip {
      flex: 1;
    }
  }
  &__content {
    height: calc(100% - 50px);
    position: relative;
  }
  &__loading {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
  }
  .hanlders-list-content, .scroll-content {
    height: 100%;
  }
  .info-icon {
    fill: #706e6b;
    height: 22px;
    width: 22px;
    margin-right: 10px;
  }
}
</style>
