/* globals Visualforce */
import config from '../config'
import HTMLUtil from 'charket-ui/dist/utils/HTMLUtil'

// network_error  can not connect to serve or timeout
// remote_data_error Server data cannot be returned to the client
// serve_error Server code error
// font_end_error The data returned by the server is incorrect
function RemoteFunctionError (type, msg, originalErrorMsg) {
  this.type = type
  this.msg = msg
  this.originalErrorMsg = originalErrorMsg
}

function removeNamespace (ns, result) {
  if (ns && ns.length > 0) {
    const nsPrefix = ns + '__'
    var re = new RegExp('(&quot;)' + nsPrefix + '(\\w+&quot;:)', 'g')
    return JSON.parse(JSON.stringify(result).replace(re, '$1$2'))
  }
  return result
}

/**
 * @private mockJsr
 * dispatches mock data after timeout period and returns promise object
 * mocks are special in that they have a forceFail Boolean
 * and a forceFailAction to handle that event
 */
const mockJsr = opts => {
  return new Promise((resolve, reject) => {
    window.setTimeout(() => {
      if (opts.forceFail) {
        reject(Error)
      }
      resolve()
    }, 100)
  })
}

const realJsr = (opts, keepNs, remoteBuffer, timeoutValue) => {
  return new Promise((resolve, reject) => {
    let ns = config.sfdc_namespace ? config.sfdc_namespace : ''
    opts.args = Array.isArray(opts.args) ? opts.args : []
    const options = {
      buffer: remoteBuffer,
      escape: true,
      timeout: timeoutValue
    }

    // create array of arguments with given method
    const method = (ns !== '') ? ns + '.' + opts.method : opts.method
    let toSend = [method]

    // for each arg, namespace if it is an object or array
    // skip the namespace check
    opts.args.forEach(arg => {
      toSend.push(arg)
    })
    // add callback wrapper
    toSend.push((result, event) => {
      if (event.result === null) {
        if (!event.statusCode) {
          if (event.xhr.isTimeout || ('onLine' in navigator && navigator.onLine === false)) {
            const remoteError = new RemoteFunctionError('network_error', 'Check your Internet connection and try again.', event.message)
            reject(remoteError)
          }
        } else {
          const remoteError = new RemoteFunctionError('remote_data_error', 'Server data cannot be returned to the client.', event.message)
          reject(remoteError)
        }
      } else {
        try {
          if (ns && !keepNs) {
            result = removeNamespace(ns, result)
          }
          if (result.ok) {
            if (result.data) {
              resolve(JSON.parse(HTMLUtil.unescape(result.data)))
            } else {
              resolve(result)
            }
          } else {
            const remoteError = new RemoteFunctionError('serve_error', result.error, result.error)
            reject(remoteError)
          }
        } catch (error) {
          const remoteError = new RemoteFunctionError('font_end_error', 'The data returned by the server is incorrect', error)
          reject(remoteError)
        }
      }
    })
    // add options
    toSend.push(options)

    // Set timeout to 120 seconds in case of uploading big files
    // Visualforce.remoting.timeout = 120000
    // do the request
    Visualforce.remoting.Manager.invokeAction.apply(Visualforce.remoting.Manager, toSend)
  })
}

/**
 * @func sfdcRemoting
 * configures JSR and dispatches either a real or false JSR call
 * @param {Object} config - object with config options
 *   {Object} actions - object linking names to JSR names (or mocks)
 *                      default is `window.cs.actions`
 *   {String} namespace - name to use for namespaces
 *                        default is empty string
 * @return {Function} - callback function
 */
const sfdcRemoting = (opts, keepNs = false, remoteBuffer = true, timeoutValue = 30000) => {
  if (typeof opts.actions !== 'object') {
    window.cs = window.cs || {}
    window.cs.actions = window.cs.actions || {}
    opts.actions = window.cs.actions
  }

  // dispatch on type
  if (window.Visualforce) {
    return realJsr(opts, keepNs, remoteBuffer, timeoutValue)
  }
  return mockJsr(config)
}

export default sfdcRemoting
