<template>
  <div class="slds-scrollable_y" id="charket-sidebar">
    <div class="slds-form-element search-section">
      <div class="slds-form-element__control slds-input-has-icon slds-input-has-icon_left-right">
        <icon name="search" icon-class-name="slds-icon slds-input__icon slds-input__icon_left slds-icon-text-default">
        </icon>
        <input type="search" placeholder="Quick Find" class="slds-input search-value" @input="filterTab" v-model="searchKey"/>
        <button class="slds-button slds-button_icon slds-input__icon slds-input__icon_right" v-show="searchKey" @click="clearSearchKey">
          <icon name="close" icon-class-name="slds-button__icon slds-icon-text-light"></icon>
        </button>
      </div>
    </div>
    <div class="slds-tree_container tree-section">
      <ul class="slds-tree" role="tree" aria-labelledby="treeheading">
        <li role="treeitem" aria-level="1" :aria-selected="selectedTab.value === item.value && !item.hasSubmenu" :aria-expanded="item.hasSubmenu" :class="{'has-title-tab': item.isTabTitle}" tabindex="0" v-for="item in tabList" @click="changeTab(item)" :key="item.value">
          <div class="slds-tree__item">
            <button class="slds-button slds-button_icon slds-m-right_x-small has-submenu-tab"
                    :class="{'submenu-tab-selected': item.isActive, 'slds-is-disabled': !item.hasSubmenu}"
                    v-if="!item.isTabTitle && item.value !== 'CharketSettingsHome'">
              <icon name="chevronright" icon-class-name="slds-button__icon slds-button__icon_small">
              </icon>
            </button>
            <span class="slds-has-flexi-truncate">
              <span class="slds-tree__item-label slds-truncate nav-label" @click.stop="" v-if="item.isTabTitle" v-html="item.label"></span>
              <span class="slds-tree__item-label slds-truncate" v-if="!item.isTabTitle" v-html="item.label"></span>
            </span>
          </div>
          <ul role="group" v-show="item.hasSubmenu && item.isActive">
            <li role="treeitem" aria-level="2" :aria-selected="selectedTab.value === submenu.value && !submenu.hasSubmenu" tabindex="1" v-for="submenu in item.submenus" :key="submenu.value" @click.stop="changeTab(submenu)">
              <div class="slds-tree__item">
                <button class="slds-button slds-button_icon slds-m-right_x-small slds-is-disabled">
                  <icon name="chevronright" icon-class-name="slds-button__icon slds-button__icon_small">
                  </icon>
                </button>
                <span class="slds-has-flexi-truncate">
                    <span class="slds-tree__item-label slds-truncate" v-html="submenu.label"></span>
                  </span>
              </div>
            </li>
          </ul>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Sidebar',
  data () {
    return {
      settingTabs: [
        { value: 'CharketSettingsHome', label: 'Charket Settings Home', isActive: false, hasSubmenu: false, isTabTitle: false },
        { value: 'LiveChat', label: 'LIVE CHAT', isActive: false, hasSubmenu: false, isTabTitle: true },
        { value: 'ChatCapacity', label: 'Chat Capacity', isActive: false, hasSubmenu: false, isTabTitle: false },
        { value: 'ChatDataSettings', label: 'Chat Data Settings', isActive: false, hasSubmenu: false, isTabTitle: false },
        { value: 'LiveChatConfigurations', label: 'Live Chat Configurations', isActive: false, hasSubmenu: false, isTabTitle: false },
        { value: 'AgentStatuses',
          label: 'Agent Statuses',
          isActive: false,
          isTabTitle: false,
          hasSubmenu: true,
          submenus: [
            { value: 'AgentStatusesSettings', label: 'Agent Statuses', isActive: false, hasSubmenu: false, isTabTitle: false },
            { value: 'StartLiveChatSettings', label: 'Start Live Chat Settings', isActive: false, hasSubmenu: false, isTabTitle: false }]
        },
        { value: 'Settings', label: 'SETTINGS', isActive: false, hasSubmenu: false, isTabTitle: true },
        { value: 'WeChatQRCode',
          label: 'WeChat QR Code',
          isActive: false,
          hasSubmenu: true,
          isTabTitle: false,
          submenus: [
            { value: 'EnabledObjects', label: 'Enabled Objects', isActive: false, hasSubmenu: false, isTabTitle: false }]
        },
        { value: 'CharketSms',
          label: 'Charket SMS',
          isActive: false,
          hasSubmenu: true,
          isTabTitle: false,
          submenus: [
            {
              value: 'CharketSmsSettings',
              label: 'Charket SMS Settings',
              isActive: false,
              hasSubmenu: false,
              isTabTitle: false
            }
          ]
        },
        { value: 'Person Account',
          label: 'Person Account',
          isActive: false,
          hasSubmenu: true,
          isTabTitle: false,
          submenus: [
            { value: 'PersonAccountSettings', label: 'Person Account Settings', isActive: false, hasSubmenu: false, isTabTitle: false }]
        },
        { value: 'Pardot',
          label: 'Pardot',
          isActive: false,
          hasSubmenu: true,
          isTabTitle: false,
          submenus: [
            { value: 'PardotSettings', label: 'Pardot Settings', isActive: false, hasSubmenu: false, isTabTitle: false },
            { value: 'FormHandler', label: 'Form Handler', isActive: false, hasSubmenu: false, isTabTitle: false }
          ]
        },
        { value: 'Marketing Cloud',
          label: 'Marketing Cloud',
          isActive: false,
          hasSubmenu: true,
          isTabTitle: false,
          submenus: [
            { value: 'ConnectedAppSettings', label: 'Connected App Settings', isActive: false, hasSubmenu: false, isTabTitle: false },
            { value: 'JWTSettings', label: 'Marketing Cloud Auth. Settings', isActive: false, hasSubmenu: false, isTabTitle: false },
            { value: 'JourneyBuilderActivities', label: 'Journey Builder Activities', isActive: false, hasSubmenu: false, isTabTitle: false }]
        },
        { value: 'Security',
          label: 'Security',
          isActive: false,
          hasSubmenu: true,
          isTabTitle: false,
          submenus: [
            { value: 'WhitelistedVisualforcePages', label: 'Whitelisted Visualforce Pages', isActive: false, hasSubmenu: false, isTabTitle: false }]
        },
        { value: 'Development', label: 'DEVELOPMENT', isActive: false, hasSubmenu: false, isTabTitle: true },
        { value: 'Custom Handlers',
          label: 'Custom Handlers',
          isActive: false,
          hasSubmenu: true,
          isTabTitle: false,
          submenus: [
            { value: 'WeChatMessageHandlers', label: 'WeChat Message Handlers', isActive: false, hasSubmenu: false, isTabTitle: false },
            { value: 'WeChatEventHandlers', label: 'WeChat Event Handlers', isActive: false, hasSubmenu: false, isTabTitle: false }]
        },
        { value: 'CharketAISettings', label: 'Charket AI Settings', isActive: false, hasSubmenu: false, isTabTitle: false },
        { value: 'WeChatWebOAuth',
          label: 'WeChat Web OAuth',
          isActive: false,
          hasSubmenu: true,
          isTabTitle: false,
          submenus: [
            { value: 'AuthorizeEndpointURL', label: 'Authorize Endpoint URL', isActive: false, hasSubmenu: false, isTabTitle: false }]
        }
      ],
      tabList: [],
      selectedTab: {},
      searchKey: ''
    }
  },
  watch: {
    '$route' () {
      this.setActiveTab(this.$route.name)
    },
    isSmsIntegrationEnabled (newValue) {
      this.setVerificationCodeTab()
    }
  },
  created () {
    this.setVerificationCodeTab()
    this.selectedTab = this.settingTabs[0]
    this.tabList = JSON.parse(JSON.stringify(this.settingTabs))
    this.setActiveTab(this.$route.name)
  },
  computed: {
    isSmsIntegrationEnabled () {
      return this.$store.state.charketSms.smsIntegrationSettings ? this.$store.state.charketSms.smsIntegrationSettings.IsCharketSmsEnabled : false
    }
  },
  methods: {
    setVerificationCodeTab () {
      const smsTab = this.tabList.find((item) => {
        return item.value === 'CharketSms'
      })
      if (!smsTab) {
        return
      }

      if (this.isSmsIntegrationEnabled) {
        smsTab.submenus.push({ value: 'VerificationCodeSettings', label: 'Verification Code Settings', isActive: false, hasSubmenu: false, isTabTitle: false })
      } else {
        const index = smsTab.submenus.findIndex((item) => item.value === 'VerificationCodeSettings')
        smsTab.submenus.splice(index, 1)
      }
    },
    clearSearchKey () {
      this.searchKey = ''
      this.filterTab()
    },
    setActiveTab (routeName) {
      if (!routeName) {
        this.selectedTab = this.settingTabs[0]
      } else {
        for (let item of this.tabList) {
          if (item.hasSubmenu) {
            let selectedItem = item.submenus.find(sub => routeName.includes(sub.value) || routeName === 'AgentStatusDetail')
            if (selectedItem) {
              item.isActive = true
              this.selectedTab = selectedItem
              return
            }
          } else {
            if (routeName === item.value) {
              this.selectedTab = item
              return
            }
          }
        }
      }
    },
    changeTab (item) {
      if (item.hasSubmenu) {
        item.isActive = !item.isActive
      } else {
        this.selectedTab = item
        if (this.$route.name !== item.value) {
          this.$bus.emit('close-toast')
        }
        this.$router.push({ name: item.value })
      }
    },
    filterTab () {
      let copyTabs = JSON.parse(JSON.stringify(this.settingTabs))

      if (this.searchKey) {
        let lowercaseSearckKey = this.searchKey.toLowerCase()
        let tempTabs = []

        let markSearchKey = (tabLabel) => {
          let matchIndex = tabLabel.toLowerCase().indexOf(lowercaseSearckKey)
          if (matchIndex <= -1) {
            return tabLabel
          }
          return [
            tabLabel.substring(0, matchIndex),
            '<mark>',
            tabLabel.substring(matchIndex, matchIndex + lowercaseSearckKey.length),
            '</mark>',
            tabLabel.substring(matchIndex + lowercaseSearckKey.length)
          ].join('')
        }

        for (let settingTab of copyTabs) {
          let settingTabLabel = settingTab.label.toLowerCase()

          if (settingTab.hasSubmenu) {
            let tab = JSON.parse(JSON.stringify(settingTab))
            tab.submenus = []

            for (let submenu of settingTab.submenus) {
              if (submenu.label.toLowerCase().indexOf(lowercaseSearckKey) > -1) {
                submenu.label = markSearchKey(submenu.label)
                tab.submenus.push(submenu)
              }
            }

            if (tab.submenus.length > 0) {
              tab.isActive = true
              tab.label = markSearchKey(tab.label)
              tempTabs.push(tab)
            } else {
              if (settingTabLabel.indexOf(lowercaseSearckKey) > -1) {
                tab.submenus = settingTab.submenus
                tab.isActive = true
                tab.label = markSearchKey(tab.label)
                tempTabs.push(tab)
              }
            }
          } else {
            if (settingTabLabel.indexOf(lowercaseSearckKey) > -1) {
              settingTab.label = markSearchKey(settingTab.label)
              tempTabs.push(settingTab)
            }
          }
        }
        this.tabList = tempTabs
      } else {
        this.tabList = copyTabs

        for (let item of this.tabList) {
          if (item.hasSubmenu) {
            let selectedItem = item.submenus.find(sub => sub.value === this.selectedTab.value)
            if (selectedItem) {
              item.isActive = true
            }
          }
        }
      }
    }
  }
}
</script>

<style lang="scss" scoped>
  #charket-sidebar {
    width: 250px;
    height: 100%;
    border: 0;
    border-right: 1px solid #dddbda;
    padding-bottom: 50px;
    scroll-behavior: smooth;
    .search-section {
      padding: 12px 12px 0 12px;
    }
    .search-value {
      height: 43px;
    }
    .tree-section {
      margin-top: 8px;
    }
    .nav-label {
      color: #706E6B;
    }
    .has-title-tab {
      pointer-events: none;
    }
    .has-submenu-tab {
      transition: All .2s ease-in-out;
      transform: rotate(-90deg);
    }
    .submenu-tab-selected {
      transform: none;
    }
    .slds-tree [role=treeitem][aria-selected=true]>.slds-tree__item {
      text-decoration: none;
    }
  }
</style>
