import { QueryService } from '../../service'

export default {
  props: {
    condition: {
      type: String,
      default () {
        return null
      }
    },
    orderBy: {
      type: String,
      default () {
        return null
      }
    }
  },
  methods: {
    search (keyword, obj) {
      return QueryService.searchRecords(keyword, obj.name, this.condition, this.orderBy)
    },
    searchWithFields (keyword, obj) {
      return QueryService.searchRecordsWithLayoutFields(keyword, obj.name, this.condition, this.orderBy)
    },
    searchRecentItems (obj) {
      return QueryService.getRecentItems(obj.name, this.condition)
    }
  }
}
