<template>
  <div id="app" class="charket charket-app-page">
    <sidebar>
    </sidebar>
    <toast
      ref="notifyToast"
      :type="toastMsg.type"
      :auto-hide="!toastMsg.hasAction"
      @close="onNotifyToastClose($event)"
    >
      <h2 class="slds-text-heading_small">
        {{ toastMsg.content }} <a v-if="toastMsg.hasAction" href="javascript:void(0);" @click="onClickToastAction()">Reload List.</a>
      </h2>
    </toast>
    <router-view transition-mode="out-in">
    </router-view>

    <!-- Global Components -->
    <confirm ref="GlobalConfirm"></confirm>
  </div>
</template>

<script>
import Sidebar from './views/Sidebar'
import { Confirm } from './global-components'
import { getCurrentUserTimeZone } from './api/charket-settings.js'
import { app } from './main.js'
import store from './vuex'

export default {
  store,
  name: 'App',
  components: {
    Sidebar,
    Confirm
  },
  provide: {
    showErrorImg (e) {
      e.target.src = require('./assets/images/error_img.png')
    }
  },
  data () {
    return {
      toastMsg: {
        content: '',
        type: '',
        hasAction: false
      }
    }
  },
  async created () {
    this.getCharketSMSSettings()
    await this.getUserTimeZone()
  },
  mounted () {
    const setToast = (msg) => {
      this.toastMsg.content = msg.content
      this.toastMsg.type = msg.type
      this.toastMsg.hasAction = !!msg.hasAction
      this.$nextTick(() => {
        this.$refs.notifyToast.open()
      })
    }
    this.$bus.on('success', (msg) => {
      setToast(msg)
    })
    this.$bus.on('error', (msg) => {
      setToast(msg)
    })
    this.$bus.on('close-toast', () => {
      this.$refs.notifyToast.hide()
    })
  },
  beforeDestroy () {
    this.$bus.off('success')
    this.$bus.off('error')
    this.$bus.off('close-toast')
  },
  methods: {
    async getCharketSMSSettings () {
      try {
        this.$store.dispatch('charketSms/getSMSIntegrationSettings')
      } catch (error) {
        this.$bus.emit('error', { content: error.message, type: 'error' })
      }
    },
    async getUserTimeZone () {
      try {
        let result = await getCurrentUserTimeZone()
        app.global_user_timezone = result.TimeZone
      } catch (error) {
        this.$bus.emit('error', { content: error.msg, type: 'error' })
      }
    },
    onClickToastAction () {
      window.location.reload()
    },
    onNotifyToastClose (e) {
      if (e.source === 'manually' && this.toastMsg.hasAction) {
        this.$bus.emit('click-toast-close')
      }
    }
  }
}
</script>

<style lang="scss">
  @import './assets/sass/main.scss';
</style>
<style lang="scss" scoped>
  .charket {
    display: flex;
    position: relative;
  }
  .charket-app-page {
    background-color: #fff;
  }
</style>
