import { MessageTransport } from '../transport'
import { Message } from '../model'

const TYPE_TRANSPORT_FUNCTION_MAPPING = new Map([
  ['Image', 'getImages'],
  ['Video', 'getVideos'],
  ['Voice', 'getVoices'],
  ['Text Message', 'getTextMessages'],
  ['Structured Message', 'getStructuredMessages'],
  ['Notification Template', 'getNotificationTemplates'],
  ['Mini Program Page', 'getMiniProgramPages']
])

const transport = new MessageTransport()

class MessageService {
  static async getMessagesByIds (ids) {
    const data = await transport.getWeChatMessages(ids)

    return data.map((item) => {
      return new Message(item)
    })
  }

  /**
   * type: Image / Video / Voice / Text Message / Mini Program Page / Structured Message / Notification Template
   */
  static async getMessages ({ type, wechatAccountId, offset = 0, limit = 20, keyword = '' }, conditions = {}) {
    const transportFn = transport[TYPE_TRANSPORT_FUNCTION_MAPPING.get(type)]
    const records = await transportFn.bind(transport)(wechatAccountId, offset, limit, keyword, conditions)
    if (!Array.isArray(records)) {
      return []
    }

    return records.map((record) => {
      return new Message(record)
    })
  }

  static getMiniProgramAppId (wechatAccountId) {
    return transport.getMiniProgramAppId(wechatAccountId)
  }

  static async createTextMessage ({ wechatAccountId, content }) {
    const { messageId } = await transport.saveTextMessage(wechatAccountId, content)

    return new Message({
      messageId: messageId,
      textBody: content,
      type: 'Text Message'
    })
  }

  static async updateTextMessage ({ id, content }) {
    const { messageId } = await transport.updateTextMessage(id, content)

    return new Message({
      messageId: messageId,
      textBody: content,
      type: 'Text Message'
    })
  }
}

export default MessageService
