import Transport from './Transport'

const ACTION_BASE_NAME = 'CharketSmsSettingsRemoteActions'

class CharketSmsSettingsTransport extends Transport {
  constructor () {
    super(ACTION_BASE_NAME)
  }

  getCharketSmsSettings () {
    return this.invoke('getCharketSmsSettings', [])
  }

  saveCharketSmsSettings (settingsJSON) {
    return this.invoke('saveCharketSmsSettings', [
      JSON.stringify(settingsJSON)
    ])
  }

  getVerificationCodeSettings () {
    return this.invoke('getVerificationCodeSettings', [])
  }

  saveVerificationCodeSettings (setingsJSON) {
    return this.invoke('saveVerificationCodeSettings', [
      JSON.stringify(setingsJSON)
    ])
  }
}
export default CharketSmsSettingsTransport
