import { MessageService } from '../../../service'

class MiniProgramLinkDataProvider {
  constructor (options) {
    this.options = options
    this.wechatAccount = options.wechatAccount
  }

  getMiniProgramAppId () {
    return MessageService.getMiniProgramAppId(this.wechatAccount.id)
  }
}

export default MiniProgramLinkDataProvider
